.page-not-found-content {
  background-size: cover;
  padding: 73px 0 150px;
  text-align: center;
  .row {
    margin: 0;
  }
  h1 {
    font-weight: bold;
    font-size: 132px;
    line-height: 180px;
    color: $white;
    margin-bottom: 0;
  }
  h2 {
    font-weight: bold;
    font-size: 32px;
    line-height: 44px;
    color: $white;
  }
  p {
    font-size: 18px;
    line-height: 25px;
    color: $white;
    margin-bottom: 48px;
  }
  .btn {
    display: inline-flex;
    align-items: center;
    padding: 0 60px;
    height: 60px;
  }
  @media (max-width: 768px) {
    padding-bottom: 100px;
    h1 {
      font-size: 116px;
      line-height: 158px;
      margin-bottom: 16px;
    }
    h2 {
      font-size: 24px;
      line-height: 33px;
      margin-bottom: 40px;
    }
    p {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 40px;
    }
    .btn {
      margin: 0 16px;
      padding: 0 40px;
    }
  }
}
