.other-brands {
  position: relative;
  padding: 136px 15px 112px;
  display: grid;
  grid-template-columns: 1fr auto;
  .brand-item {
    min-height: 430px;
  }
  .flickity-viewport {
    overflow: visible;
  }
  &__title {
    grid-column: 1 / 2;
    font-size: 38px;
    line-height: 52px;
    margin-bottom: 69px;
  }
  &__btn {
    grid-column: 2 / -1;
    display: flex;
    height: 60px;
    padding: 0 40px;
  }
  &__slider {
    grid-column: 1 / -1;
    overflow: hidden;
    width: calc(100% + 30px);
    padding: 0 15px;
    margin-left: -15px;
  }
  &__slider-button {
    background: none;
    border: none;
    position: absolute;
    top: 475px;
    padding: 0;
    &:focus {
      outline: none;
    }
    &--left {
      left: -57px;
    }
    &--right {
      right: -57px;
    }
    &:disabled {
      opacity: 0.4;
    }
  }
  @media (max-width: 1260px) {
    &__slider-button {
      &--left {
        left: -15px;
      }
      &--right {
        right: -15px;
      }
    }
  }
  @media (max-width: 1200px) {
    &__btn {
      margin-top: 20px;
    }
    &__title {
      margin-bottom: 26px;
    }
    &__slider-nav {
      grid-column: 1 / -1;
      grid-row: 2 / 3;
      margin-bottom: 26px;
      display: flex;
      justify-content: flex-end;
    }
    &__slider-button {
      position: static;
      &--left {
        margin-right: 32px;
      }
    }
  }
  @media (max-width: 991px) {
    padding: 64px 0 46px;
  }
  @media (max-width: 767px) {
    padding: 56px 16px;
    position: relative;
    grid-template-columns: 1fr;
    row-gap: 0;
    &__title {
      grid-column: 1 / -1;
      font-size: 30px;
      line-height: 41px;
      max-width: 400px;
    }
    &__btn {
      grid-column: 1 / -1;
      grid-row: 4 / 5;
      margin-top: 24px;
    }
    &__slider-nav {
      padding: 0 5px;
    }
  }
}
