.login {
  margin-bottom: 118px;

  @include media-breakpoint-up(md) {
    margin-bottom: 212px;
  }

  .breadcrumbs {
    margin: 32px 0 40px;
  }

  h1 {
    margin-bottom: 40px;
  }

  &__content {
    margin: 0 auto;
    align-items: center;
    @extend .d-flex, .justify-content-center;
  }

  &__form {
    max-width: 454px;
    width: 100%;
    margin: 0 auto;

    &__head {
      max-width: 330px;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #ebecec;
      padding-bottom: 13px;

      &__elem {
        display: flex;
        align-items: center;
        padding-bottom: 13px;

        &__text {
          font-weight: 600;
          font-size: 15px;
          line-height: 20px;
          color: $black-2;
          margin-left: 8px;
        }

        &--active {
          position: relative;

          .login__form__head__elem__text {
            color: $burgundy;
          }

          .active-icon {
            display: block !important;
          }

          .inactive-icon {
            display: none !important;
          }

          &:after {
            content: "";
            border-bottom: 3px solid #6b2a14;
            position: absolute;
            width: 100%;
            bottom: -15px;
          }
        }

        .active-icon {
          display: none;
        }

        .inactive-icon {
          display: block;
        }
      }

      &__text {
        font-size: 15px;
        line-height: 20px;
        color: #000;
      }
    }


    &__text {
      font-weight: 600;
      font-size: 18px;
      line-height: 25px;
      margin: 33px 0 24px;
      color: $black;
    }

    &__input {
      font-size: 15px;
      line-height: 20px;
      color: #333;
      padding: 19px 24px;
      background: #fff;
      border: 1px solid #ebecec;
      max-width: 454px;
      width: 100%;


      &__icon {
        position: absolute;
        right: 7%;
        top: 18px;
        cursor: pointer;

        svg {
          width: 24px;
          height: 24px;
        }

        &__hide {
          display: none;
          transition: 0.9s;
        }
      }
    }

    &__checkbox {
      margin: 30px 0;
      font-size: 15px;
      line-height: 20px;
      color: $black;
    }

    &__btn {
      padding: 19px 0;
      max-width: 454px;
      width: 100%;
      margin-bottom: 40px;
    }

    &__bottom-text {
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 20px;
      color: $black;
    }

    &__link {
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      color: $burgundy;
      margin: 16px 0 0 0;


      @include media-breakpoint-up(md) {
        margin: 0 0 0 24px;
      }
    }
    &__error-msg {
      margin-top: 10px;

      @extend .d-flex, .align-items-center;
      &__text {
        color: #d70d0d;
        font-weight: normal;
        font-size: 13px;
        line-height: 18px;
        margin-left: 12px;
      }
    }


    &__text-checkbox {
      align-items: start !important;
      color: $black;

      @include media-breakpoint-up(md) {
        align-items: center !important;
      }
    }
  }

  &__hide {
    display: none !important;
  }
  &__show {
    display: flex !important;
  }
}


.container--small {
  padding: 0 16px;
}


