.article-item {
  display: block;
  min-height: 420px;
  position: relative;
  background: $white;
  box-shadow: 0.000001px 2px 6px rgba(0, 0, 0, 0.06);
  transition: 0.3s;
  &:hover {
    box-shadow: 0.000001px 2px 8px rgba(0, 0, 0, 0.16);
    img {
      transform: scale(1.15);
    }
  }
  &__img {
    overflow: hidden;
    height: 206px;
    img {
      width: 100%;
      transition: 0.3s;
      object-fit: cover;
    }
  }
  &__info {
    padding: 16px 24px;
    &-data {
      display: block;
      font-size: 15px;
      line-height: 20px;
      color: $black-2;
      margin-bottom: 8px;
      opacity: 0.6;
    }
    &-title {
      font-weight: 600;
      font-size: 18px;
      line-height: 25px;
      color: $black;
      margin-bottom: 16px;
    }
    &-text {
      font-size: 16px;
      line-height: 26px;
      color: $black-2;
      margin-bottom: 30px;
    }
    &-category {
      position: absolute;
      bottom: 24px;
      p {
        display: inline-block;
        font-size: 15px;
        line-height: 20px;
        color: $burgundy;
        margin-right: 24px;
        margin-bottom: 0;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  @media (max-width: 1200px) {
    min-height: 450px;
    &__info-title {
      margin-bottom: 12px;
    }
    &__info-text {
      margin-bottom: 32px;
    }
  }
  @media (max-width: 992px) {
    min-height: 430px;
    &__img {
      height: 120px;
    }
    &__info {
      padding: 16px 10px;
    }
  }
  @media (max-width: 767px) {
    height: auto;
    min-height: auto;
    &__img {
      height: 206px;
    }
    &__info {
      padding: 16px 24px;
    }
    &--big {
      .article-item__img img {
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.search-page-content .article-item {
  margin-bottom: 30px;
}
