.article-content {
  h1 {
    margin-bottom: 40px;
  }
  &__share-wrapper {
    position: relative;
    &.divider:after {
      bottom: -50px;
    }
  }
  &__top-img {
    height: 400px;
    width: 100%;
    object-fit: cover;
    margin-bottom: 48px;
  }
  .col-12 > p {
    margin-bottom: 48px;
  }
  .img-box {
    margin-bottom: 40px;
    img {
      height: 530px;
      object-fit: cover;
      margin-bottom: 16px;
    }
    figcaption {
      font-size: 15px;
      line-height: 20px;
      color: $black-2;
      opacity: 0.6;
    }
  }
  &__video-yotube {
    margin-bottom: 50px;
  }
  &__navigation-bottom {
    position: relative;
  }
  &__other-articles {
    padding: 64px 0;
    .btn {
      display: inline-flex;
      padding: 0 40px;
      height: 60px;
      align-items: center;
    }
    @media (max-width: 992px) {
      padding: 56px 0;
    }
  }
  @media (max-width: 768px) {
    h1 {
      margin-bottom: 24px;
    }
    .col-12 > p {
      margin-bottom: 24px;
    }
    &__top-img {
      height: 279px;
    }
    .img-box {
      margin-bottom: 24px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__video-yotube {
      margin-bottom: 24px;
      iframe {
        height: 182px;
      }
    }
    &__share-wrapper.divider:after {
      bottom: -34px;
    }
  }
}

.article-content--place {
  padding-bottom: 64px;
  @media (max-width: 992px) {
    padding-bottom: 56px;
  }
}