.about-content {
  padding-bottom: 50px;
  img {
    height: 400px;
    width: 100%;
    object-fit: cover;
    margin-bottom: 48px;
  }
  & > p {
    margin-bottom: 48px;
  }
  @media (max-width: 768px) {
    & > p {
      margin-bottom: 24px;
    }
    img {
      height: 350px;
      margin-bottom: 32px;
    }
  }
}
