.share {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
  span,
  time {
    font-size: 18px;
    line-height: 25px;
    color: $black;
  }
  &__social {
    display: flex;
    align-items: center;
    span {
      margin-right: 24px;
    }
    &-list {
      display: inline-flex;
    }
    &-item {
      margin-right: 12px;
      width: 24px;
      height: 24px;
      a {
        display: block;
        height: 24px;
        text-align: center;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__date {
    display: flex;
    align-items: center;
    time {
      margin-left: 16px;
    }
  }
  @media (max-width: 768px) {
    display: block;
    margin-bottom: 34px;
    span,
    time {
      font-size: 15px;
      line-height: 20px;
    }
    &__social {
      display: block;
      span {
        display: block;
        margin-bottom: 16px;
      }
      &-list {
        display: flex;
        margin-bottom: 25px;
      }
    }
  }
}
