.help-content {
  padding-bottom: 50px;
  & > p {
    margin-bottom: 48px;
  }
  @media (max-width: 768px) {
    & > p {
      margin-bottom: 24px;
    }
  }
}

.question-wrapper {
  &__title {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
    cursor: pointer;
    padding: 25px 0;
    width: 100%;
    svg {
      width: 20px;
      transition: all 0.2s;
    }
  }
  &__text {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
  }
  .active svg {
    transform: rotate(180deg);
  }
}
