.carousel {
  width: 400px;
  height: 400px;
  &__item {
    width: 400px;
    height: 400px;
    margin-right: 19px;
  }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__rate {
    z-index: 10;
    display: flex;
    align-items: center;
    position: absolute;
    right: 16px;
    top: 16px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    padding: 7px 8px 8px;
    color: $white;
    background: $yellow;
    border-radius: 2px;
    span {
      margin-left: 8px;
    }
  }
}

.gallery-buttons {
  width: 400px;
  margin-top: 19px;
  &__button {
    cursor: pointer;
    width: 86px;
    height: 86px;
    margin-right: 19px;
    object-fit: cover;
    &:last-child {
      margin-right: 0;
    }
  }
}

@media (max-width: 1200px) {
  .carousel {
    width: 342px;
    height: 342px;
    &__item {
      width: 342px;
      height: 342px;
      margin-right: 10px;
    }
  }
  .gallery-buttons {
    width: 342px;
    margin-top: 10px;
    &__button {
      width: 78px;
      height: 78px;
      margin-right: 10px;
    }
  }
}

@media (max-width: 767px) {
  .carousel {
    width: 510px;
    height: 510px;
    &__item {
      width: 510px;
      height: 510px;
    }
  }
  .gallery-buttons {
    width: 510px;
  }
}

@media (max-width: 576px) {
  .carousel {
    width: 100%;
    height: 342px;
    &__item {
      width: 100%;
      height: 342px;
    }
  }
  .gallery-buttons {
    width: 100%;
    &__button {
      width: 24%;
      height: auto;
      margin-right: 1%;
    }
  }
}
