.breadcrumbs {
  display: flex;
  margin: 32px 0 64px;
  li + li::before {
    content: "-";
    padding: 0 3px;
    color: $black-2;
  }
  &__item {
    color: $black-2;
    font-size: 15px;
    line-height: 20px;
    a {
      color: $black-2;
      font-size: 15px;
      line-height: 20px;
    }
    &--active {
      color: $burgundy;
    }
  }
  @media (max-width: 992px) {
    display: block;
    &__item {
      float: left;
      &--active {
        float: none;
      }
    }
  }
  @media (max-width: 768px) {
    margin: 16px 0 32px;
    &__item {
      font-size: 12px;
      line-height: 16px;
      a {
        font-size: 12px;
        line-height: 16px;
      }
    }
  }
}
