body * {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
}

body {
  padding-top: 155px;
  @media (max-width: 1200px) {
    padding-top: 140px;
  }
  @media (max-width: 992px) {
    padding-top: 128px;
  }
  @media (max-width: 768px) {
    padding-top: 60px;
  }
}

h1 {
  color: $black;
  font-weight: bold;
  font-size: 38px;
  line-height: 52px;
  margin-bottom: 64px;
  @media (max-width: 768px) {
    font-size: 30px;
    line-height: 41px;
  }
}

h2 {
  color: $black;
  font-weight: bold;
  font-size: 30px;
  line-height: 41px;
  margin-bottom: 32px;
  @media (max-width: 768px) {
    font-size: 24px;
    line-height: 33px;
    margin-bottom: 24px;
  }
}

p {
  color: $black-2;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 30px;
  @media (max-width: 768px) {
    font-size: 16px;
    line-height: 26px;
  }
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  &:hover {
    cursor: pointer;
    text-decoration: none;
    color: initial;
  }
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.overflow-hidden {
  overflow: hidden;
}

.img-box {
  margin-bottom: 48px;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 992px) {
    margin-bottom: 24px;
  }
}

.divider {
  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: $grey;
  }
}
