.our-partners {
  padding: 104px 15px;
  &__title {
    text-align: center;
    font-weight: bold;
    font-size: 38px;
    line-height: 52px;
    margin-bottom: 82px;
  }
  &__list {
    display: flex;
    justify-content: space-between;
  }
  &__item img {
    height: 77px;
    width: auto;
  }
  @media (max-width: 1200px) {
    &__item img {
      height: 67px;
    }
  }
  @media (max-width: 992px) {
    padding: 64px 15px 72px;
    &__title {
      margin-bottom: 64px;
    }
    &__item img {
      height: 50px;
    }
  }
  @media (max-width: 768px) {
    &__list {
      display: block;
      text-align: center;
    }
    &__item {
      margin-bottom: 32px;
      &:nth-child(1) img {
        height: 91px;
      }
      &:nth-child(2) img {
        height: 107px;
      }
      &:nth-child(3) img {
        height: 49px;
      }
      &:nth-child(4) img {
        height: 99px;
      }
      &:nth-child(5) img {
        height: 67px;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
