.business-content {
  h1 {
    font-size: 34px;
    line-height: 46px;
  }
  .other-brands__btn {
    display: none;
  }
}

.business-info {
  display: grid;
  grid-template-columns: 400px 646px;
  column-gap: 64px;
  margin-bottom: 81px;
  &__title {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    margin-bottom: 32px;
  }
  &__gallery {
    grid-row: 1 / 4;
    grid-column: 1 / 2;
    margin-bottom: 32px;
  }
  &__description {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
  }
  &__our-brand {
    grid-row: 4 / 5;
    grid-column: 1 / 2;
  }
  &__map {
    grid-row: 5 / 6;
    grid-column: 1 / 2;
  }
  &__contacts {
    grid-row: 3 / 7;
    grid-column: 2 / 3;
  }
}

@media (max-width: 1200px) {
  .business-info {
    grid-template-columns: 342px 1fr;
    column-gap: 32px;
  }
}

@media (max-width: 991px) {
  .business-content {
    h1 {
      font-size: 38px;
      line-height: 52px;
    }
  }
  .business-info {
    &__title {
      grid-row: 1 / 2;
      grid-column: 1 / 3;
    }
    &__gallery {
      grid-row: 2 / 4;
    }
  }
}

@media (max-width: 767px) {
  .business-content {
    h1 {
      font-size: 30px;
      line-height: 41px;
    }
  }
  .business-info {
    display: block;
  }
}
