.brand-item {
  display: grid;
  grid-template-rows: 260px 1fr;
  width: 343px;
  margin-right: 35px;
  margin-bottom: 5px;
  margin-left: 5px;
  position: relative;
  background: $white;
  box-shadow: 0.000001px 2px 6px rgba(0, 0, 0, 0.06);
  transition: 0.3s;
  &:hover {
    box-shadow: 0.000001px 2px 8px rgba(0, 0, 0, 0.16);
    img {
      transform: scale(1.15);
    }
  }
  &__img {
    overflow: hidden;
    height: 260px;
    img {
      width: 100%;
      transition: 0.3s;
      object-fit: cover;
    }
  }
  &__rate {
    z-index: 10;
    display: flex;
    align-items: center;
    position: absolute;
    right: 16px;
    top: 16px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    padding: 7px 8px 8px;
    color: $white;
    background: $yellow;
    border-radius: 2px;
    span {
      margin-left: 8px;
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    z-index: 10;
    padding: 16px 24px;
    &-title {
      font-weight: 600;
      font-size: 21px;
      line-height: 29px;
      color: $black;
      margin-bottom: 16px;
    }
    &-text {
      font-size: 16px;
      line-height: 26px;
      color: $black-2;
      margin-bottom: 14px;
    }
    &-category {
      margin-top: auto;
      display: inline-block;
      font-size: 15px;
      line-height: 20px;
      color: $burgundy;
      margin-right: 24px;
      margin-bottom: 0;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &--wide {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 409px;
    padding: 64px;
    transition: 0.3s;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: 0.3s;
    }
    &:hover {
      img {
        transform: scale(1.15);
      }
    }
    .brand-item__rate {
      top: 32px;
      right: 32px;
    }
    .brand-item__info {
      position: absolute;
    }
    .brand-item__info-title {
      font-size: 48px;
      line-height: 65px;
      color: $white;
      margin-bottom: 32px;
      max-width: 550px;
    }
    .brand-item__info-text {
      font-size: 18px;
      line-height: 28px;
      color: $white;
      margin-bottom: 32px;
      max-width: 550px;
    }
    .btn {
      align-self: flex-start;
      display: inline-flex;
      width: auto;
      margin: 0;
      padding: 0 60px;
    }
  }
  &--high {
    position: relative;
    overflow: hidden;
    height: 947px;
    display: flex;
    align-items: flex-end;
    transition: 0.3s;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: 0.3s;
    }
    &:hover {
      img {
        transform: scale(1.15);
      }
    }
    .brand-item__info {
      padding: 72px 24px;
      &-title {
        color: $white;
        margin-bottom: 32px;
      }
      &-text {
        color: $white;
        margin-bottom: 32px;
      }
      &-category {
        position: static;
        font-size: 15px;
        line-height: 26px;
        color: $grey;
        margin-bottom: 32px;
      }
      .btn {
        margin: 0;
      }
    }
  }

  @media (max-width: 1200px) {
    grid-template-rows: 210px 1fr;
    width: 284px;
    &__img {
      height: 210px;
    }
    &--wide {
      width: 100%;
    }
  }

  @media (max-width: 991px) {
    width: 320px;
    &--wide {
      width: 99%;
      padding: 48px;
    }
  }

  @media (max-width: 768px) {
    width: 99%;
    margin-bottom: 24px;
    &--wide {
      display: none;
    }
    &--high {
      display: none;
    }
  }
}
