.main-slider {
  position: relative;
  &__wrap {
    position: absolute;
    top: 32px;
    display: flex;
    z-index: 10;
    p {
      font-size: 15px;
      line-height: 20px;
      color: $white;
      margin-bottom: 0;
    }
    svg {
      margin-right: 10px;
    }
  }
  &__data,
  &__time {
    display: flex;
  }
  &__data {
    margin-right: 48px;
  }
  &__slider {
    .slider-item .container {
      position: relative;
    }
  }
  &__search-block {
    position: absolute;
    bottom: 0;
    width: 1110px;
    padding: 24px 24px 0;
    background-color: rgba(235, 236, 236, 0.6);
    .advanced-search-button {
      color: $black;
      transition: color 0.3s;
      svg {
        margin-left: 8px;
        transition: all 0.3s;
        path {
          transition: all 0.3s;
        }
      }
      &.open {
        color: $burgundy;
        svg {
          transform: rotate(45deg);
        }
        path {
          stroke: $burgundy;
        }
      }
    }
    .search-block__form::after {
      display: none;
    }
  }

  @media (max-width: 1200px) {
    &__slider {
      .flickity-page-dots {
        right: 4%;
      }
    }
    &__search-block {
      width: 930px;
    }
  }

  @media (max-width: 991px) {
    &__search-block {
      width: 690px;
    }
  }

  @media (max-width: 768px) {
    &__search-block {
      display: none;
    }
    &__slider {
      .slider-item .container {
        padding: 0;
      }
    }
  }

  @media (max-width: 576px) {
    &__wrap {
      p {
        font-size: 12px;
        line-height: 16px;
      }
      svg {
        margin-right: 8px;
      }
    }
    &__data {
      margin-right: 32px;
    }
  }
}

.buttons-wrapper {
  position: absolute;
  z-index: 10;
}
.button-row {
  position: relative;
  &__wrapper {
    position: absolute;
    right: -30px;
    transform: rotate(90deg);
    top: 185px;
  }
  .button {
    width: 10px;
    height: 10px;
    margin: 0 6px;
    padding: 0;
    border: none;
    outline: none;
    background-color: $white;
    opacity: 0.3;
  }
  .button.is-selected {
    background-color: $yellow;
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .buttons-wrapper {
    bottom: 0;
  }
  .button-row {
    &__wrapper {
      top: auto;
      bottom: 25px;
      right: auto;
      left: 0;
      transform: rotate(0);
    }
    .button {
      margin: 0 6px 0 0;
    }
  }
}

.blink {
  animation-name: blinker;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(1, 2, 0, 1);
  animation-duration: 1s;
  -webkit-animation-name: blinker;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: cubic-bezier(1, 2, 0, 1);
  -webkit-animation-duration: 1s;
}

@keyframes blinker {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@-webkit-keyframes blinker {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
