.business-contacts {
  &__tab {
    position: relative;
  }
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    cursor: pointer;
    padding: 25px 0;
    font-weight: 600;
    font-size: 21px;
    line-height: 29px;
    color: $black;
    svg {
      transition: all 0.2s;
    }
  }
  .business-contacts__checkbox:checked ~ .business-contacts__heading svg {
    transform: rotate(180deg);
  }
  &__checkbox:checked svg {
    transform: rotate(180deg);
  }
  &__checkbox {
    display: none;
    position: absolute;
    left: -9999em;
  }
  &__checkbox:checked ~ &__content {
    max-height: 10000px;
    transition: all 2s;
  }
  &__content {
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s;
    &-item {
      display: flex;
      align-items: baseline;
      margin-bottom: 24px;
    }
  }
  &__name {
    width: 170px;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: $black-2;
  }
  &__contact {
    font-size: 16px;
    line-height: 22px;
    color: $black-2;
    margin: 0;
    span {
      display: inline-block;
    }
    svg {
      margin: 0 12px 0 0;
    }
  }
  @media (max-width: 991px) {
    &__content-item {
      display: block;
      margin-bottom: 16px;
    }
    &__name {
      margin-bottom: 8px;
    }
  }
}
