.interesting-places-content {
  padding-bottom: 30px;
  &__info {
    position: relative;
    padding-bottom: 24px;
    p {
      margin-bottom: 24px;
    }
  }
  &__search {
    padding: 64px 0 84px;
    @media (max-width: 768px) {
      padding: 48px 0;
      h2 {
        display: none;
      }
    }
  }
  h1,
  &__title-big {
    font-weight: bold;
    font-size: 38px;
    line-height: 52px;
    margin-bottom: 64px;
  }
  h2 {
    font-size: 21px;
    line-height: 28px;
  }
  @media (max-width: 768px) {
    padding-bottom: 0;
    &__info {
      p {
        margin-bottom: 24px;
      }
    }
  }
}

.places-box-wrapper {
  margin-top: 64px;
  margin-bottom: 112px;
  h2 {
    color: $black-3;
  }
  &__links-wrapper {
    padding: 0 16px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 50px 30px;
  }
  &__one-link-wrapper {
    padding: 0 16px;
  }
  @media (max-width: 992px) {
    margin-bottom: 48px;
    &__links-wrapper {
      grid-template-columns: 1fr;
    }
  }
}

.map-wrapper {
  margin-bottom: 112px;
  height: 900px;
}

.create-direction {
  color: $white !important;
}