.link-box {
  display: grid;
  grid-template-rows: 150px auto;
  background: $white;
  box-shadow: 0.0001px 2px 8px rgba(0, 0, 0, 0.16);
  &__img {
    overflow: hidden;
    height: 150px;
    img {
      width: 100%;
      transition: 0.3s;
      &:hover {
        transform: scale(1.15);
      }
    }
  }
  &__info {
    padding: 20px 20px 0;
    h2 {
      font-size: 24px;
      line-height: 33px;
      font-weight: normal;
      margin-bottom: 24px;
    }
    p {
      font-size: 14px;
      line-height: 21px;
      margin-bottom: 32px;
    }
  }
  &__nav {
    display: flex;
    margin: 0 20px 20px;
    height: 60px;
    justify-content: center;
    align-self: flex-end;
    svg {
      width: 25px;
    }
  }
  @media (max-width: 1200px) {
    grid-template-rows: 125px auto;
    &__img {
      height: 125px;
    }
  }
  /*
  @media (max-width: 992px) {
    grid-template-rows: 110px auto;
    &__img {
      height: 110px;
    }
    &__info {
      padding: 20px 20px 0;
    }
    &__nav {
      margin: 0 20px 20px;
    }
  }
  */

  @media (max-width: 992px) {
    grid-template-rows: 230px auto;
    &__img {
      height: 230px;
    }
    &__nav {
      margin: 0 16px 16px;
    }
    &__info {
      padding: 16px 16px 0;
      h2 {
        margin-bottom: 16px;
      }
      p {
        margin-bottom: 24px;
      }
    }
  }
}
