.our-brand {
  position: relative;
  padding-bottom: 32px;
  &__title {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: $yellow;
    margin-bottom: 24px;
  }
  &__text p {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 24px;
  }
  &__btn {
    height: 60px;
    display: flex;
  }
  @media (max-width: 991px) {
    padding-bottom: 24px;
    &__title {
      margin-bottom: 16px;
    }
  }
  @media (max-width: 767px) {
    padding-top: 24px;
    &__title {
      margin-bottom: 24px;
    }
  }
}
