.other-articles {
  display: grid;
  grid-template-columns: 1fr auto;
  padding-bottom: 36px;
  &__title {
    grid-column: 1 / 2;
    font-size: 38px;
    line-height: 52px;
    margin: 0;
  }
  &__btn {
    grid-column: 2 / -1;
    display: block;
  }
  &__slider-nav {
    display: none;
  }
  &__slider {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    margin-top: 68px;
  }
  @media (max-width: 992px) {
    position: relative;
    grid-template-columns: 1fr;
    padding: 0;
    &__title {
      grid-column: 1 / -1;
      font-size: 30px;
      line-height: 41px;
      margin-bottom: 26px;
    }
    .flickity-button {
      width: 24px;
      height: 24px;
      position: absolute;
      top: -26px;
      background: none;
      border-radius: 0;
      svg {
        display: none;
      }
      &.previous {
        left: inherit;
        right: 64px;
        background-image: url("data:image/svg+xml,%3Csvg width='22' height='20' viewBox='0 0 22 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M22 10C22 9.62119 21.8552 9.25789 21.5973 8.99003C21.3395 8.72217 20.9898 8.57169 20.6252 8.57169H4.69618L10.5998 2.44137C10.8579 2.17317 11.003 1.80941 11.003 1.43012C11.003 1.05083 10.8579 0.687071 10.5998 0.418872C10.3416 0.150673 9.99147 0 9.62638 0C9.26129 0 8.91115 0.150673 8.65299 0.418872L0.403912 8.98875C0.275877 9.12143 0.174297 9.27905 0.104987 9.45257C0.0356769 9.6261 0 9.81213 0 10C0 10.1879 0.0356769 10.3739 0.104987 10.5474C0.174297 10.721 0.275877 10.8786 0.403912 11.0112L8.65299 19.5811C8.91115 19.8493 9.26129 20 9.62638 20C9.99147 20 10.3416 19.8493 10.5998 19.5811C10.8579 19.3129 11.003 18.9492 11.003 18.5699C11.003 18.1906 10.8579 17.8268 10.5998 17.5586L4.69618 11.4283H20.6252C20.9898 11.4283 21.3395 11.2778 21.5973 11.01C21.8552 10.7421 22 10.3788 22 10Z' fill='%236B2A14'/%3E%3C/svg%3E%0A");
        background-repeat: no-repeat;
      }
      &.next {
        right: 0;
        background-image: url("data:image/svg+xml,%3Csvg width='22' height='20' viewBox='0 0 22 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0 10C0 9.62119 0.14485 9.25789 0.402683 8.99003C0.660517 8.72217 1.01021 8.57169 1.37485 8.57169H17.3038L11.4002 2.44137C11.1421 2.17317 10.997 1.80941 10.997 1.43012C10.997 1.05083 11.1421 0.687071 11.4002 0.418872C11.6584 0.150673 12.0085 0 12.3736 0C12.7387 0 13.0889 0.150673 13.347 0.418872L21.5961 8.98875C21.7241 9.12143 21.8257 9.27905 21.895 9.45257C21.9643 9.6261 22 9.81213 22 10C22 10.1879 21.9643 10.3739 21.895 10.5474C21.8257 10.721 21.7241 10.8786 21.5961 11.0112L13.347 19.5811C13.0889 19.8493 12.7387 20 12.3736 20C12.0085 20 11.6584 19.8493 11.4002 19.5811C11.1421 19.3129 10.997 18.9492 10.997 18.5699C10.997 18.1906 11.1421 17.8268 11.4002 17.5586L17.3038 11.4283H1.37485C1.01021 11.4283 0.660517 11.2778 0.402683 11.01C0.14485 10.7421 0 10.3788 0 10Z' fill='%236B2A14'/%3E%3C/svg%3E%0A");
        background-repeat: no-repeat;
      }
      &:disabled {
        opacity: 0.4;
      }
    }
    &__btn {
      grid-column: 1 / -1;
      grid-row: 4 / 5;
      margin-top: 24px;
    }
    &__slider-button {
      border: none;
      background: none;
      &--left {
        margin-right: 30px;
      }
      &:focus {
        outline: 0;
      }
    }
    &__slider {
      display: block;
      margin-top: 26px;
    }
    .article-item {
      height: 412px;
      width: 255px;
      margin-right: 30px;
      margin-bottom: 5px;
      &:last-child {
        margin-right: 5px;
      }
      &__img {
        height: 206px;
      }
      &__info {
        padding: 16px 24px;
      }
    }
  }
}
