.search-block {
  padding: 24px 24px 0;
  background-color: $grey-bg;
  p {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: $black;
    margin-bottom: 16px;
  }
  .tab-content__item-wrap {
    position: relative;
  }
  &__wrap {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transition: all 1s;
    &.open {
      opacity: 1;
      overflow: visible;
      max-height: 10000px;
      transition: max-height 0.3s;
      transition: opacity 1s;
    }
  }
  &__form {
    position: relative;
    padding-bottom: 14px;
    &-chip {
      margin-top: 14px;
      font-size: 15px;
      line-height: 20px;
      color: $burgundy;
      margin-right: 8px;
    }
  }
  &__tabs {
    padding: 24px 0 0;
  }
  &__settings {
    position: relative;
    padding: 24px 0;
  }
  &__select-list {
    padding-bottom: 30px;
  }
}

.search-form {
  display: block;
  input {
    height: 60px;
    font-size: 15px;
    line-height: 20px;
    color: $black-2;
    width: 82%;
    padding: 20px 24px;
    background: $white;
    border: 1px solid $grey;
    &:focus {
      outline: 0;
      border: 1px solid $yellow;
    }
  }
  button {
    height: 60px;
    display: inline-block;
    padding: 0 51px;
    position: absolute;
    background: $yellow;
    border: 1px solid $yellow;
    span {
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      color: $white;
    }
    svg {
      margin-left: 11px;
    }
    &:focus {
      outline: 0;
    }
  }
}

.nav-tabs {
  display: flex;
  &__item {
    cursor: pointer;
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    color: $black-2;
    background: $white;
    padding: 10px 24px;
    margin-right: 16px;
    &:last-child {
      margin-right: 0;
    }
    &--active {
      color: $white;
      background: $burgundy;
    }
  }
}

.tab-content {
  &__item {
    display: none;
    &--active {
      display: block;
    }
    &--1 {
      .checkbox {
        margin-bottom: 30px;
      }
    }
    &--3 {
      .tab-content__item-wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
      }
      .search-block__select-list {
        width: 260px;
      }
      .search-block__checkbox-list {
        padding-bottom: 30px;
      }
    }
  }
}

.checkbox {
  display: inline-block;
  margin-right: 35px;
  label {
    font-size: 15px;
    line-height: 20px;
    color: $black;
  }
  .custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  .custom-checkbox + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
  }
  .custom-checkbox + label::before {
    margin-right: 12px;
    content: "";
    width: 22px;
    height: 22px;
    border-radius: 5px;
    display: inline-block;
    flex-shrink: 0;
    flex-grow: 0;
    border: 2px solid $burgundy;
    background-repeat: no-repeat;
    background-position: center center;
  }
  .custom-checkbox:not(:disabled):not(:checked) + label:hover::before {
    cursor: pointer;
  }
  .custom-checkbox:checked + label::before {
    background-image: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.166 1.56651C13.3163 1.41629 13.4007 1.21255 13.4007 1.00011C13.4007 0.787666 13.3163 0.583926 13.166 0.433707C13.0158 0.283489 12.8121 0.199097 12.5996 0.199097C12.3872 0.199097 12.1835 0.283489 12.0332 0.433707L4.59965 7.86891L1.96604 5.23371C1.81583 5.08349 1.61209 4.9991 1.39964 4.9991C1.1872 4.9991 0.983462 5.08349 0.833244 5.23371C0.683025 5.38393 0.598633 5.58767 0.598633 5.80011C0.598633 6.01255 0.683025 6.21629 0.833244 6.36651L4.03324 9.56651C4.10756 9.64101 4.19584 9.70012 4.29303 9.74045C4.39022 9.78078 4.49442 9.80154 4.59965 9.80154C4.70487 9.80154 4.80907 9.78078 4.90626 9.74045C5.00345 9.70012 5.09173 9.64101 5.16605 9.56651L13.166 1.56651Z' fill='%236B2A14'/%3E%3C/svg%3E");
  }
}

.chips__item {
  min-height: 44px;
  display: inline-flex;
  background: $white;
  padding: 12px 24px;
  margin: 24px 24px 24px 0;
  p {
    font-weight: normal;
    font-size: 15px;
    line-height: 20px;
    color: $black;
    margin: 0;
  }
  &:last-child {
    margin-right: 0;
  }
  svg {
    margin-left: 16px;
    cursor: pointer;
    padding: 5px 0;
  }
  &:first-child {
    display: none;
  }
  &:hover {
    background: #fff9f8;
    p {
      color: $burgundy;
    }
  }
}

.custom-select {
  &.vscomp-ele {
    max-width: 233px;
    margin-right: 24px;
    &:last-child {
      margin-right: 0;
    }
  }
  .vscomp-ele-wrapper {
    height: 44px;
    color: $black-3;
    background: $white;
    border: 1px solid $grey;
    outline: none;
  }
  .vscomp-value {
    opacity: 1;
    color: $black-3;
    font-size: 15px;
    line-height: 20px;
  }
  .vscomp-wrapper {
    &:not(.has-value) .vscomp-value {
      opacity: 1;
    }
    .checkbox-icon {
      display: none;
    }
    .show-value-as-tags .vscomp-toggle-button {
      padding: 0 24px;
    }
    &.focused .vscomp-toggle-button,
    &:focus .vscomp-toggle-button {
      box-shadow: none;
    }
  }
  .vscomp-option {
    padding: 0 12px;
    border-bottom: 1px solid $grey;
    &.focused {
      background-color: $white;
      cursor: pointer;
    }
    &.selected {
      background-color: $white;
      background-repeat: no-repeat;
      background-position: calc(100% - 20px);
      background-image: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.166 1.56651C13.3163 1.41629 13.4007 1.21255 13.4007 1.00011C13.4007 0.787666 13.3163 0.583926 13.166 0.433707C13.0158 0.283489 12.8121 0.199097 12.5996 0.199097C12.3872 0.199097 12.1835 0.283489 12.0332 0.433707L4.59965 7.86891L1.96604 5.23371C1.81583 5.08349 1.61209 4.9991 1.39964 4.9991C1.1872 4.9991 0.983462 5.08349 0.833244 5.23371C0.683025 5.38393 0.598633 5.58767 0.598633 5.80011C0.598633 6.01255 0.683025 6.21629 0.833244 6.36651L4.03324 9.56651C4.10756 9.64101 4.19584 9.70012 4.29303 9.74045C4.39022 9.78078 4.49442 9.80154 4.59965 9.80154C4.70487 9.80154 4.80907 9.78078 4.90626 9.74045C5.00345 9.70012 5.09173 9.64101 5.16605 9.56651L13.166 1.56651Z' fill='%236B2A14'/%3E%3C/svg%3E") !important;
    }
  }
  .vscomp-arrow {
    right: 10px;
    &::after {
      border: none;
      transform: rotate(0deg);
      transition: all 0.2s;
      width: 16px;
      height: 16px;
      margin-top: -3px;
      background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.45202 13.046C7.51318 13.1348 7.59502 13.2075 7.69048 13.2576C7.78594 13.3078 7.89217 13.334 8.00002 13.334C8.10787 13.334 8.2141 13.3078 8.30956 13.2576C8.40502 13.2075 8.48686 13.1348 8.54802 13.046L14.548 4.37933C14.6175 4.27937 14.6582 4.16228 14.6658 4.0408C14.6734 3.91932 14.6475 3.79808 14.591 3.69026C14.5345 3.58244 14.4496 3.49216 14.3454 3.42923C14.2412 3.36631 14.1217 3.33314 14 3.33333H2.00002C1.87858 3.33383 1.75958 3.36743 1.65581 3.4305C1.55203 3.49358 1.46742 3.58375 1.41106 3.69132C1.35471 3.79889 1.32874 3.91979 1.33596 4.04102C1.34317 4.16224 1.3833 4.2792 1.45202 4.37933L7.45202 13.046Z' fill='%236B2A14'/%3E%3C/svg%3E%0A");
    }
  }
  .focused .vscomp-arrow::after {
    transform: rotate(-180deg);
  }
  .vscomp-toggle-button {
    border: none;
  }
  .multiple.has-value {
    .vscomp-value {
      font-size: 0;
    }
    .vscomp-selected-value-count {
      font-size: 15px;
      &::after {
        content: " ÑÐ°Ð¹Ð¾Ð½Ð¸";
      }
      &.one-district::after {
        content: " ÑÐ°Ð¹Ð¾Ð½";
      }
    }
  }
}

@media (max-width: 1200px) {
  .search-form input {
    width: 77%;
  }
}

@media (max-width: 992px) {
  .search-form input {
    width: 70%;
  }
  .tab-content__item--3 .search-block__select-list {
    width: 100%;
    padding: 0;
  }
  .custom-select.vscomp-ele {
    max-width: 200px;
    margin-right: 15px;
  }
  .tab-content__item--3 {
    .checkbox:last-child {
      margin-top: 20px;
    }
  }
}

@media (max-width: 768px) {
  .search-form {
    input {
      width: 100%;
      margin-bottom: 8px;
    }
    button {
      position: static;
      display: block;
      width: 100%;
      height: 60px;
    }
  }
  .nav-tabs {
    display: block;
    &__item {
      width: 100%;
      margin-bottom: 8px;
      text-align: center;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .search-block {
    &__title {
      margin-bottom: 24px;
    }
    &__settings {
      padding: 24px 0;
    }
    &__tabs {
      padding-bottom: 0;
    }
    &__chips {
      padding: 24px 0;
    }
  }
  .custom-select {
    &.vscomp-ele {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .tab-content__item--3 {
    .tab-content__item-wrap {
      display: block;
    }
    .search-block__select-list {
      width: 100%;
    }
    .search-block__checkbox-list {
      margin-top: 30px;
    }
  }
  .checkbox {
    width: 100%;
  }
  .chips__item {
    margin: 0 12px 12px 0;
  }
}

.advanced-search-button {
  display: inline-block;
  padding: 10px;
  cursor: pointer;
}

/* Main search block on menu */
.search-block--main {
  display: none;
  position: absolute;
  padding: 0;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  .search-block__content {
    width: 1110px;
    margin: 120px auto 0;
    padding: 24px 24px 0;
    background-color: $grey-bg;
  }
  &.open {
    display: block;
    z-index: 100;
  }
  @media (max-width: 1200px) {
    .search-block__content {
      width: 960px;
    }
  }
  @media (max-width: 992px) {
    .search-block__content {
      width: 690px;
    }
  }
  @media (max-width: 768px) {
    display: none;
    &.open {
      display: none;
    }
  }
}

.advanced-search-button {
  transition: color 0.3s;
  svg {
    margin-left: 8px;
    transition: all 0.3s;
    path {
      transition: all 0.3s;
    }
  }
  &.open {
    color: $black;
    svg {
      transform: rotate(45deg);
    }
    path {
      stroke: $black;
    }
  }
}

/* Search block on index.html */
.search-block--index {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: all 1s;
  .search-block {
    &__form {
      display: none;
    }
  }
  &.open {
    opacity: 1;
    overflow: visible;
    max-height: 10000px;
    transition: max-height 0.3s;
    transition: opacity 1s;
  }

  @media (max-width: 768px) {
    max-height: none;
    opacity: 1;
    overflow: visible;
    .search-block {
      &__form {
        display: block;
      }
    }
  }
}

/* Search block on catalog.html */
.search-block--businesses {
  .checkbox {
    margin-bottom: 30px;
  }
}

/* Search block on intresting-page.html */
.search-block--places {
  .tab-content__item {
    display: block;
  }
  .tab-content__item-wrap {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .search-block__select-list {
    width: 260px;
  }
  .search-block__checkbox-list {
    padding-bottom: 30px;
  }
  @media (max-width: 992px) {
    .search-block__select-list {
      padding: 0;
    }
    .checkbox:last-child {
      margin-top: 20px;
    }
  }
  @media (max-width: 768px) {
    .tab-content__item-wrap {
      display: block;
    }
    .search-block__select-list {
      width: 100%;
    }
    .search-block__checkbox-list {
      margin-top: 30px;
    }
  }
}

/* Search block on news.html */
.search-block--news {
  padding: 0;
  background-color: $white;
  & > p {
    margin-bottom: 24px;
  }
  .search-block__tabs {
    padding-bottom: 52px;
  }
  .chips__item {
    background: rgba(235, 236, 236, 0.3);
  }
  .search-news__wrap {
    display: flex;
    justify-content: space-between;
  }
  .tab-content__item {
    display: block;
    width: 80%;
  }
  .amount {
    width: 17%;
    .custom-select {
      max-width: 190px;
    }
  }
  @media (max-width: 1200px) {
    .custom-select {
      margin-bottom: 24px;
    }
  }
  @media (max-width: 768px) {
    .search-news__wrap {
      display: block;
    }
    .custom-select,
    .amount .custom-select {
      max-width: 100%;
    }
    .tab-content__item,
    .calendar-select,
    .amount {
      width: 100%;
    }
  }
}

/* Range calendar */
.calendar-select {
  position: relative;
  display: inline-block;
  height: 44px;
  width: 260px;
  input {
    width: 100%;
    height: 100%;
    font-size: 15px;
    line-height: 20px;
    color: $black-2;
    cursor: pointer;
    background-color: $white;
    border: 1px solid $grey;
    padding: 12px 24px;
    &:focus {
      outline: 0;
      border: 1px solid $yellow;
    }
  }
  button {
    pointer-events: none;
    height: 44px;
    width: 44px;
    background-color: $burgundy;
    position: absolute;
    top: -1px;
    right: 0;
    padding: 0 13px;
    border: none;
    outline: none;
  }
  @media (max-width: 768px) {
    width: 100%;
  }
}
