.add-brand {
  margin-bottom: 48px;

  @include media-breakpoint-up(md) {
    margin-bottom: 155px;
  }

  &__title {
    margin: 32px 0 40px;

    @include media-breakpoint-up(md) {
      margin: 64px 0 56px;
    }

    h1 {
      margin-bottom: 0;
    }

    &__btn {
      border: 1px solid #e8ba06;
      box-sizing: border-box;
      height: 60px;
      font-style: normal;
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      color: $yellow;
      max-width: 401px;
      width: 100%;
      margin-top: 40px;

      @extend .d-flex, .align-items-center, .justify-content-center;

      @include media-breakpoint-up(lg) {
        margin-top: 0;
      }

      svg {
        margin-left: 16px;
        width: 21px;
        height: 17px;
      }

      .icon-hover {
        display: none;
      }

      &:hover {
        color: $white;
        background: $yellow;

        .icon-hover {
          display: block;
        }

        .icon {
          display: none;
        }
      }
    }
  }

  .col-12 {
    padding-right: 0;
    padding-left: 0;

    @include media-breakpoint-up(md) {
      padding-right: 15px;
      padding-left: 15px;
    }
  }

  &__content {
    background: #f3f4f4;
    width: 100%;

    &__padding-big {
      padding: 16px 16px 32px;

      @include media-breakpoint-up(md) {
        padding: 32px 32px 80px;
      }
    }

    &__padding-small {
      padding: 16px 32px 80px;
    }

    &__inform-block {
      padding: 20px 16px 24px;
      background: $yellow;
      margin-bottom: 8px;

      @include media-breakpoint-up(md) {
        padding: 26px 32px 25px;
        margin-bottom: 0;
      }

      svg {
        max-width: 40px;
        width: 100%;
        height: 40px;

        @extend .d-md-block, .d-none;
      }

      &__text {
        margin-left: 0;
        font-size: 13px;
        line-height: 19px;
        color: $black;

        @include media-breakpoint-up(md) {
          margin-left: 24px;
          font-size: 16px;
          line-height: 23px;
        }
      }
    }
  }

  &__form {
    padding: 0 0;

    @include media-breakpoint-up(md) {
      padding: 0 32px;
    }

    &__text {
      font-weight: 600;
      font-size: 18px;
      line-height: 25px;
      color: $black;
      margin: 24px 0 24px;

      @include media-breakpoint-up(md) {
        margin: 48px 0 24px;
      }
      &.tablet-size {
        @media (max-width: 991px) {
          margin-bottom: 48px;
        }
        @media (max-width: 767px) {
          margin-bottom: 24px;
        }
      }
    }

    &__input {
      background: $white;
      border: 1px solid #ebecec;
      box-sizing: border-box;
      color: rgba(51, 51, 51, 0.6);
      padding: 20px 24px;
      width: 100%;
      font-size: 15px;
      line-height: 20px;
      &:focus {
        outline: 0;
        border: 1px solid $yellow;
      }
      &__small {
        max-width: 100%;

        @include media-breakpoint-up(md) {
          max-width: 454px;
        }
      }
    }

    &__textarea {
      max-width: 982px;
      width: 100%;
      height: 92px;
      padding: 20px 24px;
      font-weight: normal;
      font-size: 15px;
      line-height: 20px;
      color: rgba(51, 51, 51, 0.6);
      border: 1px solid #ebecec;
      resize: none;
      &:focus {
        outline: 0;
        border: 1px solid $yellow;
      }
      @include media-breakpoint-up(md) {
        height: 172px;
      }
    }

    .vscomp-toggle-button {
      padding: 20px 24px;
    }

    &__text-input {
      margin-right: 74px;
      @media (max-width: 992px) {
        margin-right: 20px;
      }
    }

    &__checkbox {
      padding: 28px 0 24px;
      width: 100%;

      @include media-breakpoint-up(md) {
        padding: 52px 0 44px;
      }
    }

    &__border-bottom {
      border-bottom: 1px solid #ebecec;
    }

    &__btn {
      margin: 24px auto 0;
      padding: 20px;
      max-width: 366px;
      width: 100%;

      @include media-breakpoint-up(md) {
        margin: 48px auto 0;
      }
    }
  }

  &__modal {
    top: 54%;
    height: auto;

    @include media-breakpoint-up(sm) {
      top: 50%;
    }
  }
}
