.news-content {
  .article-item {
    margin-bottom: 64px;
    @media (max-width: 992px) {
      margin-bottom: 34px;
    }
    @media (max-width: 768px) {
      margin-bottom: 24px;
    }
  }
}

.mec-calendar {
  border: none;
  margin-bottom: 104px;
  .mec-calendar-side {
    background: $white;
    box-shadow: 0.0001px 3px 5px rgba(9, 30, 66, 0.2), 0.0001px 0.0001px 1px rgba(9, 30, 66, 0.31);
    border-radius: 3px;
    padding: 32px;
    .mec-calendar-table {
      min-height: 0;
    }
    .mec-next-month,
    .mec-previous-month {
      top: 32px;
      width: 24px;
      height: 24px;
      border: none;
      box-shadow: none;
    }
    .mec-previous-month {
      left: 32px;
      border-radius: 0;
    }
    .mec-next-month {
      right: 32px;
      border-radius: 0;
    }
    .mec-calendar-side .mec-next-month:hover,
    .mec-calendar-side .mec-previous-month:hover {
      background-color: $white;
    }
  }
  .mec-load-month-link {
    font-size: 0;
  }
  .mec-month-container {
    border-spacing: 8px 0.0001px;
  }
  .mec-calendar-header {
    width: 383px;
    margin: 0;
    margin-bottom: 32px;
    h2 {
      text-transform: capitalize;
      font-weight: bold;
      font-size: 19px;
      line-height: 26px;
      color: $black;
    }
  }
  dl {
    margin: 0 auto;
  }
  dt {
    width: 41px;
    height: 32px;
    border-radius: 0;
    padding: 6px 4px;
    font-size: 16px;
    line-height: 22px;
  }
  .mec-calendar-table-head {
    margin-bottom: 24px;
    dt {
      font-weight: bold;
      font-size: 13px;
      line-height: 16px;
      text-transform: capitalize;
      height: 16px;
    }
  }
  .mec-calendar-row {
    margin-bottom: 16px;
    dt:hover {
      background-color: $white;
      color: $burgundy;
      font-weight: bold;
    }
  }
  .mec-table-nullday {
    color: #6b778c;
  }
  .mec-calendar-day {
    color: #172b4d;
  }
  .mec-has-event:after {
    display: none;
  }
  .mec-has-event-a {
    color: $burgundy;
    font-weight: normal;
    border-bottom: 2px solid rgba(107, 42, 20, 0.6);
    a {
      border-radius: 0;
    }
  }
  .mec-calendar-row dt.mec-has-event.mec-selected-day a {
    font-weight: bold;
    background-color: $white;
    color: $burgundy;
    border-bottom: 2px solid rgba(107, 42, 20, 0.6);
  }

  .mec-calendar-events-side {
    padding: 40px 40px 40px 56px;
    border: none;
    .mec-table-side-title {
      text-align: left;
      text-transform: capitalize;
      letter-spacing: inherit;
      margin-bottom: 32px;
      .mec-table-side-day {
        border: none;
        font-weight: bold;
        font-size: 19px;
        line-height: 26px;
      }
      .mec-event-time {
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
      }
      .mec-sl-clock-o {
        margin-right: 8px;
      }
      svg {
        margin-right: 16px;
      }
    }
  }
  .mec-event-article {
    border: none;
    padding: 0;
    margin-bottom: 32px;
    .mec-sl-clock-o {
      margin-right: 8px;
    }
    .mec-event-time {
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      color: $burgundy;
      margin-bottom: 16px;
    }
    .mec-event-title {
      margin: 0 0 16px;
      a {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: $black;
        text-transform: capitalize;
      }
    }
    .mec-event-detail span {
      font-weight: normal;
      font-size: 16px;
      line-height: 26px;
      color: $black-2;
    }
    &:hover {
      background-color: $white;
    }
  }
  .mec-event-footer {
    display: none;
  }
}
.mec-wrap.colorskin-custom .mec-calendar .mec-calendar-row dt.mec-has-event:hover,
.mec-wrap.colorskin-custom .mec-calendar:not(.mec-event-calendar-classic) .mec-selected-day {
  background-color: $white;
}

.mec-wrap.mec-sm959 .mec-calendar:not(.mec-event-calendar-classic):not(.mec-calendar-weekly) dt {
  height: auto !important;
  line-height: inherit !important;
}

.mec-calendar dt {
  height: auto !important;
  line-height: inherit !important;
}

@media (max-width: 992px) {
  .mec-calendar .mec-calendar-header {
    width: 327px;
  }
}

@media (max-width: 768px) {
  .mec-calendar {
    .mec-calendar-side {
      padding: 10px;
    }
    .mec-calendar-header {
      width: 100%;
    }
    .mec-calendar-events-side {
      padding: 20px 12px;
    }
    dt {
      width: 26px;
      height: 26px;
      font-size: 14px;
      line-height: 19px;
    }
  }
}
