.business-map {
  .gmnoprint {
    display: none;
  }
  .gm-fullscreen-control {
    display: none;
  }
  margin-top: 32px;
  &__title {
    font-size: 18px;
    line-height: 25px;
    color: $yellow;
    margin-bottom: 24px;
  }
  &__map {
    width: 400px;
    height: 400px;
  }
  &__btn {
    margin-top: 24px;
    height: 60px;
  }
  @media (max-width: 1200px) {
    &__map {
      width: 342px;
      height: 342px;
    }
  }
  @media (max-width: 767px) {
    margin-top: 24px;
    position: relative;
    padding-bottom: 24px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 0;
      background: $grey;
    }
    &__map {
      width: 100%;
    }
  }
}