.edit-brand {
  .vscomp-option {
    padding: 0 24px;
    border-bottom: 1px solid $grey;
    &.focused {
      background-color: $white;
      cursor: pointer;
    }
    &.selected {
      background-color: $white;
      background-repeat: no-repeat;
      // background-position: 420px center;
      background-position: calc(100% - 2%);
      background-image: url("data:image/svg+xml,%3Csvg width='14' height='10' viewBox='0 0 14 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.166 1.56651C13.3163 1.41629 13.4007 1.21255 13.4007 1.00011C13.4007 0.787666 13.3163 0.583926 13.166 0.433707C13.0158 0.283489 12.8121 0.199097 12.5996 0.199097C12.3872 0.199097 12.1835 0.283489 12.0332 0.433707L4.59965 7.86891L1.96604 5.23371C1.81583 5.08349 1.61209 4.9991 1.39964 4.9991C1.1872 4.9991 0.983462 5.08349 0.833244 5.23371C0.683025 5.38393 0.598633 5.58767 0.598633 5.80011C0.598633 6.01255 0.683025 6.21629 0.833244 6.36651L4.03324 9.56651C4.10756 9.64101 4.19584 9.70012 4.29303 9.74045C4.39022 9.78078 4.49442 9.80154 4.59965 9.80154C4.70487 9.80154 4.80907 9.78078 4.90626 9.74045C5.00345 9.70012 5.09173 9.64101 5.16605 9.56651L13.166 1.56651Z' fill='%236B2A14'/%3E%3C/svg%3E") !important;
    }
  }
  .custom-select {
    max-width: 100%;
  }
  .vscomp-ele-wrapper {
    height: 60px;
  }
  &__add-address {
    &.hidden {
      display: none;
    }
  }
  textarea {
    @media (max-width: 768px) {
      height: 124px;
    }
  }
  .checkbox.margin-top {
    margin-bottom: 30px;
  }
  &__form {
    scrollbar-width: none;
  }
}

.add-address__btn {
  position: relative;
  margin-top: 24px;
  margin-bottom: unset;
  font-size: 15px;
  line-height: 20px;
  width: fit-content;
  cursor: pointer;
  &::after {
    content: url(../../images/svg-icons/plus.svg);
    position: absolute;
    top: 3px;
    right: -31px;
    margin-left: 19px;
  }
}
.add-object {
  margin-top: 48px;
  &__title-button {
    display: grid;
    grid-template-columns: 1fr auto;
    margin: 48px 0;
    padding: 54px 0;
    border-top: 1px solid #ebecec;
    border-bottom: 1px solid #ebecec;
    @media (max-width: 768px) {
      margin: 24px 0;
      padding: 24px 0;
    }
  }
  &__title {
    margin-bottom: unset;
    @media (max-width: 768px) {
      font-size: 32px;
      line-height: 40px;
      padding-right: 80px;
      margin-bottom: 24px;
    }
  }
  &__button {
    margin-bottom: unset;
    padding: 0 60px;
    height: 60px;
  }
  h3 {
    font-size: 22px;
    line-height: 30px;
    font-weight: 700;
    .object-number {
      font-size: 22px;
      line-height: 30px;
      font-weight: 700;
    }
  }
  .delete-object {
    margin-bottom: unset;
    position: relative;
    padding-right: 32px;
    font-size: 15px;
    line-height: 20px;
    cursor: pointer;
    &::after {
      content: url(../../images/svg-icons/trash.svg);
      position: absolute;
      top: 2px;
      right: 0;
    }
  }
  @media (max-width: 992px) {
    &__button {
      margin-bottom: unset;
      padding: 0 20px;
      height: 60px;
    }
  }
  @media (max-width: 768px) {
    &__title-button {
      display: block;
    }
    &__button {
      width: 100%;
    }
  }
}
.add-object,
.add-brand__online {
  &.hidden {
    display: none;
  }
}
.category-button {
  overflow-y: scroll;
  padding-right: 40px;
  position: relative;
  height: 60px;
  color: #1e1e1e;
  font-size: 15px;
  line-height: 20px;
  cursor: pointer;
  scrollbar-width: none;
  &::after {
    content: url(../../images/svg-icons/category-icon.svg);
    position: absolute;
    top: calc(50% - 9px);
    right: 20px;
  }
}
.category-button::-webkit-scrollbar {
  width: 0;
}


.category {
  &__modal {
    position: fixed;
    padding: 32px;
    height: 497px;
    width: 100%;
    max-width: 982px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 30;
    background-color: #fff;
    transition: 0.3s all;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  &__head {
    margin-bottom: 24px;
  }
  &__title {
    font-size: 21px;
    line-height: 28px;
    font-weight: 700;
    @media (max-width: 768px) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__close-modal {
    width: 44px;
    height: 44px;
    padding: 14px 0 0 15px;
    flex-shrink: 0;
    background: #e8ba062e;
    transition: 0.3s;
    &:hover {
      background: #e57937;
    }
  }
  &__list {
    border-top: 1px solid #ebecec;
    border-bottom: 1px solid #ebecec;
    padding: 12px 0;
    @media screen and (max-width: 992px) {
      overflow-y: scroll;
    }
  }
  &__item {
    label {
      margin: 12px 24px 12px 0;
      font-size: 18px;
      line-height: 25px;
      font-weight: 600;
      cursor: pointer;
      background: #ebecec;
      padding: 16px 32px;
      @media (max-width: 768px) {
        margin: unset;
      }
      &.checked {
        background: #e8ba06;
        color: #fff;
      }
    }
    label > input {
      display: none;
    }
    @media (max-width: 768px) {
      font-size: 16px;
      line-height: 21px;
      margin: 8px 16px 8px 0;
    }
  }
  &__button-submit {
    color: #fff;
    padding: 20px 40px 20px 40px;
    margin-top: 24px;
  }
}

.file__drop {
  padding: 40px;
  height: 293px;
  border: 1px solid #ebecec;
  background: #fff;
  &-title {
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    color: #333;
    text-align: center;
  }
  &-text {
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #333;
    opacity: 0.5;
  }
  @media (max-width: 992px) {
    padding: 40px 20px;
    &-title,
    &-text {
      max-width: 277px;
    }
  }
}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__wrapper {
  position: relative;
  padding: 10px 57px 10px 32px;
  border: 1px solid #e8ba06;
  color: #e8ba06;
  cursor: pointer;
  &::after {
    content: url(../../images/svg-icons/upload.svg);
    position: absolute;
    top: calc(50% - 8px);
    right: 16px;
  }
  .input__file,
  .input__button {
    z-index: -2;
    cursor: pointer;
  }
}
.files__wrpap {
  margin-top: 8px;
  overflow-y: scroll;
}
.files__wrpap::-webkit-scrollbar {
  width: 0;
}
.file-style {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  padding: 18px 27px;
  margin-bottom: 24px;
  .filenam {
    padding: 0 20px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    overflow: hidden;
  }
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}
.vscomp-value {
  white-space: revert;
}


