.search-page-content {
  h1 {
    margin-bottom: 48px;
    font-size: 38px;
    line-height: 52px;
  }
  &__head-wrapper {
    display: flex;
    justify-content: space-between;
    margin: 64px 0;
    h2 {
      margin: 0;
      font-size: 38px;
      line-height: 52px;
    }
    .btn {
      display: flex;
      height: 60px;
      padding: 0 40px;
    }
  }
  &__brands-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 104px;
  }
  &__articles-wrapper {
    margin-bottom: 104px;
  }
  &__places-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
    margin-bottom: 64px;
  }
  &__one-place-wrapper {
    margin-bottom: 112px;
  }
  &__concretized-title {
    margin: 64px 0;
    font-size: 38px;
    line-height: 52px;
  }
  &__concretized-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 35px;
    margin-bottom: 72px;
    margin-left: -5px;
  }
  &__text-no-results {
    margin: 100px 0 140px;
    text-align: center;
    h2 {
      font-size: 32px;
      line-height: 44px;
      color: $black-2;
    }
  }
  /*
  @media (max-width: 1200px) {
    &__brands-wrapper .brand-item {
      height: 467px;
    }
  }
  */
  @media (max-width: 991px) {
    &__brands-wrapper {
      grid-template-columns: repeat(2, 1fr);
      margin-bottom: 56px;
      .brand-item {
        margin-bottom: 40px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &__concretized-wrapper {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media (max-width: 768px) {
    h1 {
      margin-bottom: 32px;
      font-size: 30px;
      line-height: 41px;
    }
    &__head-wrapper {
      display: block;
      margin: 32px 0;
      h2 {
        margin-bottom: 16px;
        font-size: 30px;
        line-height: 41px;
      }
    }
    &__brands-wrapper {
      display: block;
      margin-bottom: 32px;
      .brand-item {
        margin-bottom: 24px;
      }
    }
    &__articles-wrapper .article-item {
      margin-bottom: 24px;
    }
    &__places-wrapper {
      display: block;
      margin-bottom: 32px;
      .place-box {
        margin-bottom: 32px;
      }
    }
    &__one-place-wrapper {
      margin-bottom: 48px;
    }
    &__concretized-title {
      margin: 32px 0;
      font-size: 30px;
      line-height: 41px;
    }
    &__concretized-wrapper {
      display: block;
    }
    &__text-no-results {
      h2 {
        font-size: 30px;
        line-height: 41px;
      }
    }
  }
}

