.recovery {
  .breadcrumbs {
    margin: 32px 0 40px;
  }

  h1 {
    margin-bottom: 40px;
  }

  &__text {
    font-size: 16px;
    line-height: 23px;
    color: $black-2;
    margin-bottom: 24px;
    max-width: 603px;

    @include media-breakpoint-up(md) {
      margin-bottom: 40px;
    }
  }


  &__btn {
    margin-top: 24px;
    max-width: 454px;
    width: 100%;


    &__desk {
      display: none;
      @include media-breakpoint-up(md) {
        display: block;
      }
    }

    &__mob {
      display: block;
      @include media-breakpoint-up(md) {
        display: none;
      }
    }
  }
}

.setting-password {
  &__btn {
    margin-top: 32px;
  }
}

.confirm-password {
  &__text {
    max-width: 783px;
    width: 100%;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    color: $black-2;
    margin-bottom: 40px;
    @include media-breakpoint-up(md) {
      font-size: 18px;
      line-height: 31px;
    }
  }

  &__btn {
    max-width: 347px;
    width: 100%;
  }
}
