.catalog-content {
  h1 {
    margin-bottom: 48px;
  }
  &__search {
    padding-bottom: 72px;
    .tab-content__item {
      display: block;
    }
  }
  &__map {
    padding: 85px 0;
  }
  &__brands {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row dense;
    gap: 48px 33px;
    margin-bottom: 64px;
  }
  .brand-item {
    margin-right: 0;
    &--high {
      grid-row: span 2;
    }
    &--wide {
      grid-column: span 3;
      grid-column: 1 / -1;
    }
    &:nth-child(10) {
      grid-column: -1 / -2;
    }
  }

  @media (max-width: 991px) {
    &__brands {
      grid-template-columns: repeat(2, 1fr) !important;
      gap: 48px 35px;
      margin-bottom: 56px;
    }
    .brand-item {
      &--wide {
        grid-column: span 2;
      }
    }
  }

  @media (max-width: 768px) {
    &__brands {
      display: block;
      margin-bottom: 24px;
    }
  }
}
