.about-city {
  padding: 80px 15px 116px;
  &__add-text p {
    width: 670px;
    margin: 0 auto 96px;
    text-align: center;
  }
  &__wrapper {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0;
  }
  &__info {
    width: 400px;
    margin-right: 20px;
  }
  &__title {
    font-weight: bold;
    font-size: 38px;
    line-height: 52px;
    margin-bottom: 40px;
  }
  &__btn {
    display: inline-flex;
    padding: 0 40px;
    height: 60px;
    margin-top: 10px;
  }
  &__video {
    width: 654px;
    height: auto;
  }

  @media (max-width: 1200px) {
    &__info {
      width: 330px;
    }
  }
  @media (max-width: 991px) {
    padding: 80px 16px;
    &__add-text p {
      margin-bottom: 80px;
      width: 100%;
    }
    &__wrapper {
      display: block;
    }
    &__info {
      width: 100%;
      margin-bottom: 40px;
    }
    &__video {
      width: 100%;
    }
  }
  @media (max-width: 768px) {
    padding: 32px 16px 56px;
    &__add-text p {
      font-size: 16px;
      line-height: 25px;
      margin-bottom: 32px;
    }
    &__wrapper {
      padding: 0 16px;
    }
    &__info {
      margin-bottom: 32px;
    }
    &__title {
      font-size: 30px;
      line-height: 41px;
      margin-bottom: 24px;
    }
    &__text {
      margin-bottom: 24px;
    }
    &__btn {
      width: 100%;
    }
  }
}
