.navigation-bottom {
  padding: 54px 0;
  display: flex;
  justify-content: space-between;
  &__link {
    display: flex;
    align-items: center;
    gap: 17px;
    position: relative;
    svg {
      position: absolute;
      transition: 0.3s;
    }
    &--prev {
      padding-left: 40px;
      svg {
        left: 0;
      }
      &:hover svg {
        left: -3px;
      }
    }
    &--next {
      padding-right: 40px;
      svg {
        right: 0;
      }
      &:hover svg {
        right: -3px;
      }
    }
    .navigation-bottom__text {
      font-weight: 600;
      font-size: 15px;
      line-height: 22px;
      color: $black-2;
      margin-bottom: 5px;
    }
    .navigation-bottom__title {
      font-weight: 600;
      font-size: 15px;
      line-height: 22px;
      color: $burgundy;
      margin-bottom: 5px;
    }
  }
  @media (max-width: 768px) {
    padding: 24px 0;
    &__link div {
      width: 80px;
    }
    &__title {
      display: none;
    }
  }
}
