.pagination {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__arrow {
    position: relative;
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    color: $burgundy;
    svg {
      position: absolute;
      transition: 0.3s;
    }
    &--prev {
      opacity: 0.4;
      span {
        padding-left: 40px;
      }
      svg {
        left: 0;
      }
      &:hover svg {
        left: -3px;
      }
    }
    &--next {
      span {
        padding-right: 40px;
      }
      svg {
        right: 0;
      }
      &:hover svg {
        right: -3px;
      }
    }
    &:hover {
      color: $burgundy;
    }
  }
  &__link {
    display: inline-block;
    text-align: center;
    width: 20px;
    margin: 0 5px;
    font-size: 21px;
    line-height: 29px;
    color: $black-2;
    &--active {
      font-weight: 600;
      color: $burgundy;
    }
    &:hover {
      color: $burgundy;
    }
  }
  @media (max-width: 768px) {
    margin-bottom: 70px;
    &__arrow {
      span {
        display: none;
      }
    }
  }
}
