.about-project {
  padding: 112px 0;
  background-size: cover;
  &__wrapper {
    width: 720px;
  }
  &__title {
    font-size: 38px;
    line-height: 52px;
    color: $white;
    font-weight: bold;
    margin-bottom: 0;
  }
  &__text p {
    width: 640px;
    color: $white;
    margin: 40px 0;
  }
  &__btn {
    display: inline-flex;
    height: 60px;
    padding: 0 48px;
  }
  @media (max-width: 768px) {
    padding: 80px 0;
    &__wrapper {
      width: 100%;
    }
    &__title {
      font-size: 30px;
      line-height: 41px;
    }
    &__text p {
      font-size: 16px;
      line-height: 25px;
      width: 100%;
      margin: 23px 0 24px;
    }
    &__btn {
      width: 100%;
      padding: 0;
    }
  }
}
