  background-repeat: no-repeat;
  background-position: top right;
  &__top {
    padding: 64px 0 40px;
  }
  &__nav {
    margin-bottom: 64px;
  }
  &__about-company {
    p {
      font-size: 13px;
      line-height: 23px;
      color: $grey;
    }
  }
  &__companies {
    padding-top: 20px;
    &-wrapper {
      display: flex;
      flex-wrap: nowrap;
      justify-content: center;
      li {
        margin-right: 24px;
        img {
          height: 48px;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  &__bottom {
    padding: 40px 0 30px;
    position: relative;
    a {
      font-size: 14px;
      line-height: 19px;
      color: $white;
      &:hover {
        cursor: pointer;
        color: $white;
      }
    }
    p {
      font-size: 14px;
      line-height: 19px;
      color: $white;
      opacity: 0.4;
    }
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      position: absolute;
      top: 0;
      left: 0;
      background: $grey;
      opacity: 0.2;
    }
  }
  @media (max-width: 992px) {
    &__companies-wrapper li img {
      height: 38px;
    }
    &__nav {
      margin-bottom: 45px;
    }
  }
  @media (max-width: 768px) {
    &__companies-wrapper {
      li {
        margin-right: 10px;
        img {
          height: 22px;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
    &__bottom {
      padding-bottom: 120px;
      a {
        margin: 40px 0 0;
      }
    }
  }
  @media (max-width: 576px) {
    padding: 32px 50px 40px;
    &__companies-wrapper {
      li {
        margin-right: 5px;
        img {
          height: 20px;
        }
      }
    }
  }
}

.footer-navigation {
  &__menu {
    display: flex;
    flex-wrap: nowrap;
    &-list {
      margin-right: 120px;
    }
    &-item {
      margin-bottom: 20px;
      a {
        font-size: 14px;
        line-height: 19px;
        text-transform: uppercase;
        color: $white;
        transition: 0.3s;
        &:hover {
          color: $yellow;
        }
      }
    }
  }
  &__contacts {
    &-title {
      font-weight: 600;
      font-size: 14px;
      line-height: 19px;
      text-transform: uppercase;
      color: $white;
      margin-bottom: 20px;
    }
    &-item {
      span {
        width: 20px;
        margin-right: 12px;
      }
      a,
      p {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 19px;
        color: $grey;
      }
      margin-bottom: 20px;
    }
  }
  @media (max-width: 1200px) {
    &__menu-list {
      margin-right: 80px;
    }
  }
  @media (max-width: 992px) {
