.footer__top {
  padding: 64px 0 40px;
}

.footer__nav {
  margin-bottom: 64px;
}

.footer__about-company p {
  font-size: 13px;
  line-height: 23px;
  color: #ebecec;
}

.footer__companies {
  padding-top: 20px;
}

.footer__companies-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.footer__companies-wrapper li {
  margin-right: 24px;
}

.footer__companies-wrapper li img {
  height: 48px;
}

.footer__companies-wrapper li:last-child {
  margin-right: 0;
}

.footer__bottom {
  padding: 40px 0 30px;
  position: relative;
}

.footer__bottom a {
  font-size: 14px;
  line-height: 19px;
  color: #fff;
}

.footer__bottom a:hover {
  cursor: pointer;
  color: #fff;
}

.footer__bottom p {
  font-size: 14px;
  line-height: 19px;
  color: #fff;
  opacity: .4;
}

.footer__bottom:before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 0;
  left: 0;
  background: #ebecec;
  opacity: .2;
}

@media (max-width: 992px) {
  .footer__companies-wrapper li img {
    height: 38px;
  }

  .footer__nav {
    margin-bottom: 45px;
  }
}

@media (max-width: 768px) {
  .footer__companies-wrapper li {
    margin-right: 10px;
  }

  .footer__companies-wrapper li img {
    height: 22px;
  }

  .footer__companies-wrapper li:last-child {
    margin-right: 0;
  }

  .footer__bottom {
    padding-bottom: 120px;
  }

  .footer__bottom a {
    margin: 40px 0 0;
  }
}

@media (max-width: 576px) {
  .footer {
    padding: 32px 50px 40px;
  }

  .footer__companies-wrapper li {
    margin-right: 5px;
  }

  .footer__companies-wrapper li img {
    height: 20px;
  }
}

.footer-navigation__menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.footer-navigation__menu-list {
  margin-right: 120px;
}

.footer-navigation__menu-item {
  margin-bottom: 20px;
}

.footer-navigation__menu-item a {
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  color: #fff;
  -webkit-transition: .3s;
  transition: .3s;
