  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
  overflow: hidden;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
}

input[type=number] {
  -moz-appearance: textfield;
}

.vscomp-value {
  white-space: revert;
}

.days__close-modal {
  width: 44px;
  height: 44px;
  padding: 14px 0 0 15px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  background: #e8ba062e;
  -webkit-transition: .3s;
  transition: .3s;
}

.days__close-modal:hover {
  background: #e57937;
}

.days__modal {
  position: fixed;
  padding: 32px;
  height: 497px;
  width: 100%;
  max-width: 982px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 30;
  background-color: #fff;
  -webkit-transition: .3s all;
  transition: .3s all;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.days__modal .to-backend {
  display: none;
}

