.calendar-modal {
  display: none;
  position: fixed;
  padding: 0;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  &.open {
    display: block;
    z-index: 105;
  }
  &__content {
    position: relative;
    width: 895px;
    margin: 120px auto 0;
    padding: 32px;
    background-color: $white;
  }
  &__title {
    position: relative;
    padding-bottom: 27px;
    font-weight: bold;
    font-size: 22px;
    line-height: 30px;
    color: $black;
  }
  &__nav-buttons.divider:after {
    display: none;
  }
  &__close-button {
    position: absolute;
    top: 23px;
    right: 32px;
    width: 44px;
    height: 44px;
    background-color: rgba(232, 186, 6, 0.2);
    border: none;
    &:focus {
      outline: none;
    }
  }
  .calendar-input {
    display: none;
  }
  .apply-button {
    display: none;
  }
  &__mobile-content {
    display: none;
  }
  @media (max-width: 992px) {
    &__content {
      width: 650px;
      margin-top: 60px;
    }
  }
  @media (max-width: 767px) {
    &__content {
      width: 343px;
      margin-top: 30px;
      padding: 24px;
    }
    &__title {
      padding-right: 70px;
      font-size: 16px;
      line-height: 22px;
      padding-bottom: 24px;
    }
    &__descktop-content {
      display: none;
    }
    &__mobile-content {
      display: block;
      padding-top: 32px;
      padding-bottom: 32px;
    }
    .calendar-input {
      display: block;
    }
    .mobile-calendar-item {
      position: relative;
      &__label {
        pointer-events: none;
        position: absolute;
        right: 0;
        padding: 13px;
        height: 44px;
        width: 44px;
        background-color: $burgundy;
      }
      &__input {
        outline: none;
        height: 44px;
        width: 100%;
        padding-left: 024px;
        background: $white;
        border: 1px solid $grey;
        color: rgba(51, 51, 51, 0.7);
      }
      &:first-child {
        margin-bottom: 32px;
      }
    }
    &__nav-buttons {
      position: relative;
      padding-top: 24px;
      button {
        width: 100%;
        height: 60px;
        &:first-child {
          margin-bottom: 16px;
        }
      }
      &.divider:after {
        display: block;
        top: 0;
      }
    }
    .apply-button {
      display: block;
    }
  }
  @media (max-width: 350px) {
    &__content {
      width: 310px;
    }
  }
}

.litepicker {
  .container {
    &__months {
      width: 100% !important;
      background: none;
      box-shadow: none;
      border-radius: 0;
      .reset-button {
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        align-items: center;
        padding: 0 40px;
        height: 60px;
        color: $yellow !important;
        background: transparent !important;
        border: 1px solid $yellow !important;
        z-index: 100;
        &:hover {
          background: $yellow !important;
          color: $white !important;
        }
      }
    }
    &__footer {
      margin-top: 65px;
      position: relative;
      background: none;
      border: none;
      border-radius: 0;
      box-shadow: none;
      .button-apply {
        cursor: pointer;
        display: flex;
        position: absolute;
        right: 0;
        bottom: 0;
        align-items: center;
        padding: 0 40px !important;
        margin: 0 !important;
        height: 60px;
        border-radius: 0 !important;
        outline: none;
        font-size: 0;
        background-color: $yellow !important;
        border: 1px solid $yellow !important;
        transition: 0.3s;
        &::before {
          content: "ÐÑÐ´ÑÐ²ÐµÑÐ´Ð¸ÑÐ¸ Ð¿ÐµÑÑÐ¾Ð´";
          font-weight: 600;
          font-size: 15px;
          line-height: 20px;
          color: $white;
        }
        &::after {
          content: "";
          display: inline-block;
          width: 20px;
          height: 20px;
          margin-left: 16px;
          background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.333 11.884v-8.55c0-.92-.748-1.667-1.666-1.667h-2.5V.417a.416.416 0 1 0-.834 0v1.25h-7.5V.417a.416.416 0 1 0-.833 0v1.25H1.667C.747 1.667 0 2.414 0 3.333v13.334c0 .919.748 1.666 1.667 1.666h10.217A4.576 4.576 0 0 0 15.417 20 4.589 4.589 0 0 0 20 15.417c0-1.42-.65-2.692-1.667-3.533Zm-17.5-8.55c0-.46.374-.834.834-.834H5v.833a.834.834 0 1 0 .833.834V2.5h7.5v.833a.834.834 0 1 0 .834.834V2.5h2.5c.46 0 .833.374.833.833v2.5H.833v-2.5ZM1.667 17.5a.834.834 0 0 1-.834-.833v-10H17.5v4.672a4.55 4.55 0 0 0-2.083-.506 4.589 4.589 0 0 0-4.584 4.584c0 .75.185 1.457.506 2.083H1.667Zm13.75 1.667a3.754 3.754 0 0 1-3.75-3.75 3.754 3.754 0 0 1 3.75-3.75 3.754 3.754 0 0 1 3.75 3.75 3.754 3.754 0 0 1-3.75 3.75Z' fill='%23fff'/%3E%3Cpath d='M17.084 15h-1.25v-1.25a.416.416 0 1 0-.833 0V15h-1.25a.416.416 0 1 0 0 .834H15v1.25a.416.416 0 1 0 .833 0v-1.25h1.25a.416.416 0 1 0 0-.834Z' fill='%23fff'/%3E%3C/svg%3E");
          background-repeat: no-repeat;
        }
        &:hover {
          background: $orange !important;
          border: 1px solid $orange !important;
        }
      }
      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 1px;
        position: absolute;
        top: -60px;
        left: 0;
        background: $grey;
      }
    }
  }
  .month-item {
    padding-top: 22px !important;
    width: 45% !important;
    &:first-child {
      margin-right: 60px;
      position: relative;
      &::after {
        content: "";
        display: block;
        width: 1px;
        height: 86%;
        position: absolute;
        top: 32px;
        right: -35px;
        background: $grey;
        z-index: 100;
      }
    }
    &-name {
      text-transform: capitalize;
    }
    &-name,
    &-year {
      font-weight: bold;
      font-size: 19px;
      line-height: 26px;
      color: $black;
    }
    &-weekdays-row {
      margin-bottom: 32px;
      div {
        text-transform: capitalize;
        width: 41px;
        height: 16px;
        margin: 0 16px;
        font-weight: bold;
        font-size: 13px;
        line-height: 16px;
        color: $black;
      }
    }
  }
  .container__days {
    div {
      width: 41px;
      height: 32px;
      margin: 0 0 13px 12px;
      border-radius: 0;
      font-size: 16px;
      line-height: 22px;
      color: #172b4d;
    }
    .day-item.is-today {
      color: $burgundy;
    }
    .day-item.is-start-date,
    .day-item.is-in-range,
    .day-item.is-end-date {
      border-radius: 0;
      color: $burgundy;
      background: rgba(235, 236, 236, 0.3);
    }
    .day-item:hover {
      color: inherit;
      box-shadow: none;
    }
  }
  .button-previous-month,
  .button-next-month {
    width: 24px;
    height: 24px;
    outline: none;
    svg {
      display: none;
    }
  }
  .button-previous-month {
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23fff' fill-opacity='.01' d='M0 0h24v24H0z'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='m9.006 10.995 4.593-4.593a.99.99 0 1 1 1.4 1.4l-3.9 3.9 3.9 3.9a.99.99 0 0 1-1.4 1.4L9.006 12.41a1 1 0 0 1 0-1.414Z' fill='%236B2A14'/%3E%3C/svg%3E");
  }
  .button-next-month {
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23fff' fill-opacity='.01' d='M0 0h24v24H0z'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.995 10.995a1 1 0 0 1 0 1.414l-4.593 4.593a.99.99 0 0 1-1.4-1.4l3.9-3.9-3.9-3.9a.99.99 0 0 1 1.4-1.4l4.593 4.593Z' fill='%236B2A14'/%3E%3C/svg%3E");
  }
  .button-cancel {
    display: none;
  }
  @media (max-width: 992px) {
    .month-item {
      &:first-child {
        margin-right: 30px;
        &::after {
          height: 86%;
          top: 32px;
          right: -15px;
        }
      }
      &-weekdays-row {
        margin-bottom: 23px;
        div {
          margin: 0 10px;
        }
      }
    }
    .container__days {
      div {
        width: 17px;
        margin: 0 10px 10px 10px;
        color: #172b4d;
      }
    }
  }
}

.timepicker-modal {
  .to-backend {
    display: none;
  }
  .timepicker-input {
    display: none;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
  &__inputs {
    display: flex;
    align-items: center;
    p {
      margin: unset;
      padding-left: 10px;
      margin-right: 20px;
    }
    .hasTime {
      width: 100px;
      height: fit-content;
    }
    @media (max-width: 768px) {
      display: block;
      p {
        margin-top: 20px;
      }
    }
  }
  .btn {
    height: 60px;
    padding: 0 40px;
  }
  @media (max-width: 768px) {
    &__wrapper {
      display: block;
    }
    .flatpickr-calendar {
      width: 100%;
    }
    .btn {
      width: 100%;
      margin-top: 16px;
    }
  }
}


