.main-menu {
  z-index: 100;
  position: fixed;
  top: 0;
  width: 100%;
  height: 152px;
  transition: top 0.3s;
  &__top {
    background: $burgundy;
  }
  &__bottom {
    background: $white;
    box-shadow: 0.0000001px 2px 4px rgba(0, 0, 0, 0.05);
  }
  @media (max-width: 768px) {
    height: 60px;
  }
}

.top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  &__buttons {
    display: none;
  }
  &__logo {
    display: none;
  }
  &__lang {
    display: flex;
    margin-right: auto;
  }
  &__search {
    display: flex;
    align-items: center;
    padding: 10px;
    margin-right: 50px;
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    color: $white;
    &:hover {
      color: $white;
    }
    svg {
      margin-left: 16px;
    }
  }
  &__personal-office {
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    color: $white;
    background: rgba(255, 255, 255, 0.1);
    padding: 11px 24px;
    svg {
      margin-left: 16px;
      width: 20px;
      height: 20px;
    }
    &:hover {
      color: $white;
    }
  }
}

.bottom-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0 17px;
  &__search {
    display: none;
  }
  &__lang {
    display: none;
  }
  &__menu-list {
    margin-left: auto;
  }
  &__logo svg {
    width: 142px;
    height: 60px;
  }
  &__add-business {
    padding: 10px 36px;
  }
}

.menu-list {
  display: flex;
  &__item {
    margin-right: 32px;
    a {
      font-size: 15px;
      line-height: 20px;
      color: $black-2;
    }
    &--active a {
      color: $yellow;
    }
    &:hover a {
      color: $yellow;
    }
  }
}

.switch-lang {
  /*display: flex;*/
  align-items: center;
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    &:checked + .switch-lang__cover > .switch-lang__circle {
      transform: translate3d(8px, 0, 0);
    }
  }
  &__lang {
    transition: opacity 0.2s;
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: $white;
    opacity: 0.4;
    &--active {
      opacity: 1;
    }
  }
  &__cover {
    margin: 0 6px;
    box-sizing: border-box;
    width: 17px;
    height: 6px;
    cursor: pointer;
    border-radius: 26px;
    transition: background-color 0.4s;
    background: rgba(255, 255, 255, 0.4);
    position: relative;
  }
  &__circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $white;
    transition: transform 0.3s ease;
    pointer-events: none;
    position: relative;
    top: -2px;
    z-index: 2;
  }
}

@media (max-width: 1200px) {
  .bottom-menu {
    padding: 10px 0;
  }
  .menu-list__item {
    margin-right: 16px;
    a {
      font-size: 14px;
    }
  }
}

@media (max-width: 992px) {
  .bottom-menu {
    &__logo svg {
      width: 95px;
      height: 48px;
    }
    &__add-business {
      padding: 8px 10px;
      font-size: 14px;
    }
  }

  .menu-list__item {
    margin-right: 10px;
    a {
      font-size: 12px;
      line-height: 16px;
    }
  }
}

@media (max-width: 768px) {
  .main-menu__top {
    background: $white;
    height: 60px;
    box-shadow: 0.0000001px 2px 4px rgba(0, 0, 0, 0.05);
  }
  .main-menu__bottom {
    display: block;
    padding: 16px 24px 32px;
    background: $white;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    overflow-y: auto;
    width: 100%;
    padding-top: 75px;
    transform: translateX(-100%);
    transition: transform 0.35s ease-in-out;
    z-index: -1;
    &.open {
      transform: translateX(0);
    }
  }
  .top-menu {
    padding: 10px 24px;
    &__buttons {
      align-self: baseline;
      margin-top: 10px;
      display: block;
      position: relative;
      button {
        padding: 0;
        width: 24px;
        height: 24px;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        border: none;
        background: none;
        outline: 0;
        transition: transform 0.2s ease-in-out;
        &:focus {
          outline: 0;
        }
      }
      .burger-button {
        transform: scale(1);
      }
      .button-close {
        transform: scale(0);
      }
      &.open-menu {
        .burger-button {
          transform: scale(0);
        }
        .button-close {
          transform: scale(1);
        }
      }
    }
    &__logo {
      display: block;
      width: 95px;
      height: 40px;
    }
    &__lang {
      display: none;
    }
    &__search {
      display: none;
    }
    &__personal-office {
      align-self: center;
      padding: 0;
      span {
        display: none;
      }
      svg {
        margin: 0;
        width: 24px;
        height: 24px;
        path {
          fill: $burgundy;
        }
      }
    }
  }
  .bottom-menu {
    display: block;
    &__search {
      position: relative;
      display: block;
      margin-bottom: 24px;
      input {
        font-size: 13px;
        line-height: 18px;
        width: 100%;
        padding: 11px 27px 11px 16px;
        background: $white;
        border: 1px solid $grey;
        &:focus {
          outline: 0;
          border: 1px solid $yellow;
        }
      }
      button {
        position: absolute;
        right: 0;
        border: 1px solid $yellow;
        background: $yellow;
        width: 42px;
        height: 42px;
        &:focus {
          outline: 0;
        }
      }
    }
    &__logo {
      display: none;
    }
    &__add-business {
      height: 60px;
      width: 100%;
      padding: 20px 0;
      margin-bottom: 36px;
    }
    &__lang {
      display: flex;
      .switch-lang {
        &__lang {
          color: $burgundy;
        }
        &__cover {
          background: rgba(107, 42, 20, 0.4);
        }
        &__circle {
          background-color: $burgundy;
        }
      }
    }
  }
  .menu-list {
    display: block;
    &__item {
      margin-bottom: 24px;
      a {
        font-size: 18px;
        line-height: 25px;
      }
    }
  }
}
