.business-description {
  &__info {
    position: relative;
    &::after {
      bottom: -32px;
    }
  }
  &__category {
    font-size: 15px;
    line-height: 20px;
    color: $burgundy;
    margin-bottom: 32px;
  }
  &__text {
    margin-bottom: 32px;
    p {
      font-size: 16px;
      line-height: 26px;
    }
  }
  &__order {
    position: relative;
    display: grid;
    grid-template-columns: 290px 1fr;
    column-gap: 33px;
    padding: 33px 0;
  }
  &__title {
    font-weight: 600;
    font-size: 21px;
    line-height: 29px;
    margin: 0;
  }
  &__btn {
    height: 60px;
  }
  @media (max-width: 991px) {
    &__category,
    &__text,
    &__title {
      margin-bottom: 24px;
    }
    &__title {
      max-width: 291px;
    }
    &__order {
      display: block;
      padding: 24px 0;
    }
  }
}
