.personal {
  margin-bottom: 138px;

  @include media-breakpoint-up(md) {
    margin-bottom: 167px;
  }

  .breadcrumbs {
    margin: 32px 0 40px;
  }


  &__title {
    margin-bottom: 32px;

    h1 {
      margin-bottom: 0;
    }

    &__text-btn {
      margin-top: 32px;
      height: auto;

      @include media-breakpoint-up(lg) {
        margin-top: 0;
        height: 52px;
      }
    }

    &__icon-text {
      margin-right: 18px;
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      color: $black-2;

      @include media-breakpoint-up(sm) {
        margin-right: 48px;
      }

      svg {
        margin-right: 12px;
        min-width: 28px;
      }
    }

    &__btn {
      background: rgba(232, 186, 6, 0.1);
      border: 1px solid #e8ba06;
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      color: $yellow;
      padding: 11px 23px;
      max-height: 40px;

      &:hover {
        color: $orange;
        border: 1px solid $orange;
        background: rgba(229, 121, 55, 0.1);
      }

      &__show-message {
        display: block !important;
      }

      &--disabled {
        background: rgba(51, 51, 51, 0.02);
        border: 1px solid rgba(51, 51, 51, 0.2);
        box-sizing: border-box;
        color: rgba(51, 51, 51, 0.6);

        &:hover {
          background: rgba(51, 51, 51, 0.02);
          border: 1px solid rgba(51, 51, 51, 0.2);
          color: rgba(51, 51, 51, 0.6);
        }
      }
    }
  }

  &__message {
    position: absolute;
    right: 0;
    display: none;
    background: $white;
    box-shadow: 0 2px 20px 3px rgba(0, 0, 0, 0.03);
    max-width: 586px;
    width: 100%;
    z-index: 100;
    top: 108%;

    &__title {
      border-bottom: none;

      @include media-breakpoint-up(md) {
        border-bottom: 1px solid #ebecec;
      }

      &__content {
        margin: 14px 24px 0;
        padding-bottom: 24px;
        border-bottom: 1px solid #ebecec;

        @include media-breakpoint-up(md) {
          margin: 20px 32px 0;
          padding-bottom: 16px;
          border-bottom: none;
        }

        @extend .d-flex, .justify-content-between, .align-items-center;
      }

      &__text {
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        color: $black-2;
        margin-left: 12px;
      }

      &__close {
        background-color: rgba(232, 186, 6, 0.2);
        width: 44px;
        height: 44px;
        cursor: pointer;

        @extend .d-flex, .align-items-center, .justify-content-center;
      }
    }

    &__content {
      padding: 16px 32px 32px 32px;
    }

    &__list {
      padding: 0 24px 24px;

      @include media-breakpoint-up(md) {
        padding: 0 32px 32px;
      }
    }

    &__item {
      border-bottom: 1px solid #ebecec;
      padding: 16px 0;
      max-height: 100%;
      height: 100%;

      @include media-breakpoint-up(md) {
        max-height: 80px;
      }

      &:nth-last-child(1) {
        border-bottom: none;
        padding: 16px 0 0;
      }

      &__img {
        margin-right: 16px;
        width: 48px;
      }

      &__title {
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        color: #000;
        margin-bottom: 8px;
      }

      &__text {
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;

        &--reject {
          color: #e57937;
        }

        &--Ñonfirm {
          color: #83a348;
        }
      }

      &__date {
        margin-top: 16px;
        font-size: 12px;
        line-height: 16px;
        color: $black-2;

        @include media-breakpoint-up(md) {
          margin-top: 0;
        }
      }
    }
  }

  &__content {
    width: 100%;

    .col-lg-8 {
      padding-left: 0;
      padding-right: 0;

      @include media-breakpoint-up(sm) {
        padding-left: 15px;
        padding-right: 15px;
      }

      @include media-breakpoint-up(lg) {
        padding-left: 9px;
      }
    }

    &__left {
      width: 100%;
    }

    &__block {
      background: #f3f4f4;
      padding: 24px;
      margin-bottom: 16px;
      width: 100%;

      @include media-breakpoint-up(md) {
        width: 335px;
      }

      @include media-breakpoint-up(lg) {
        width: 100%;
      }


      &__title {
        border-bottom: 1px solid #ebecec;
        padding-bottom: 15px;
        @extend .d-flex, .align-items-center;

        &__text {
          font-weight: 600;
          font-size: 18px;
          line-height: 25px;
          color: $black-2;
          margin-left: 16px;
        }

        &__icon {
          transform: rotate(180deg);
          transition: 0.3ms;
        }
      }

      &__settings {
        margin: 22px 0 21px;
        @extend .d-flex, .align-items-center;

        &--active {
          .personal__content__block__settings__number {
            display: block;
          }

          .personal__content__block__settings__text {
            color: $yellow;
            font-weight: 600;
          }
        }

        &__text {
          font-size: 14px;
          line-height: 19px;
          margin-right: 8px;
          color: $black-2;
        }

        &__number {
          font-weight: 600;
          font-size: 12px;
          line-height: 16px;
          background: rgba(232, 186, 6, 0.2);
          padding: 4px 9px 4px 8px;
          color: $yellow;
          display: none;
        }
      }

      &__text {
        font-size: 14px;
        line-height: 19px;
        color: $black-2;

        &--active {
          color: $yellow;
          font-weight: 600;

          &:hover {
            color: $yellow;
          }
        }
      }

      &__btn {
        font-size: 15px;
        line-height: 20px;
        letter-spacing: 0.02em;
        background: $yellow;
        padding: 10px 0 11px;
        text-align: center;
        max-width: 302px;
        width: 100%;
        color: $white;
        margin-top: 32px;
        border: none;
        transition: 300ms;

        &:hover {
          background: #e57937;
        }
      }

      &__link {
        font-size: 14px;
        line-height: 19px;
        color: $black-2;
        display: block;
        margin-top: 24px;
        border: none;
        background: none;
        padding: 0;

        &--active {
          color: $yellow;
          font-weight: 600;

          &:hover {
            color: $yellow;
          }
        }
      }
    }

    &__right {
      background: #f3f4f4;
      width: 100%;
      padding: 16px 16px 32px 16px;

      @include media-breakpoint-up(sm) {
        padding: 32px 32px 40px 32px;
      }
    }

    &__item {
      padding-bottom: 16px;
      border-bottom: 1px solid #ebecec;
      margin: 0 auto 16px;

      @include media-breakpoint-up(sm) {
        padding-bottom: 24px;
        margin: 0 auto 24px;
      }

      .col-2 {
        padding-right: 9px;
        padding-left: 0;
        max-width: 87px;
        width: 100%;
      }

      .col-md-10 {
        padding-right: 0;
        max-width: 565px;
        width: 100%;
      }

      &:nth-last-child(1) {
        margin-bottom: 0;
        border-bottom: 1px solid #ebecec;
        padding-bottom: 16px;

        @include media-breakpoint-up(sm) {
          border-bottom: none;
          margin-bottom: 0;
          padding-bottom: 0;
        }
      }

      &__img {
        img {
          width: 78px;
        }
      }

      &__status-brand {
        font-weight: 600;
        font-size: 11px;
        line-height: 15px;
        padding: 4px 10px 5px;
        display: inline-block;

        svg {
          margin-right: 8px;
        }

        &--active {
          background: rgba(131, 163, 72, 0.2);
          color: #83a348;
        }

        &--time {
          color: #e57937;
          background: rgba(229, 121, 55, 0.2);
        }

        &--inactive {
          color: #000;
          background: #ececec;
        }
      }

      &__text {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: $black;
        margin: 8px 0;
      }


      &__date {
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $black-2;
        margin-bottom: 16px;
      }

      &__link {
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: 0.02em;
        padding: 9px 0 10px;
        text-align: center;
        margin-right: 0;
        margin-bottom: 8px;
        max-width: 343px;
        width: 100%;
        background: #f3f4f4;

        @include media-breakpoint-up(sm) {
          width: 343px;
        }

        @include media-breakpoint-up(md) {
          width: 180px;
          margin-right: 8px;
        }

        @include media-breakpoint-up(xl) {
          margin-bottom: 0;
        }

        &:nth-last-child(1) {
          margin-right: 0;
          margin-bottom: 0;
        }

        &--active {
          color: $yellow;
          border: 1px solid #e8ba06;
          box-sizing: border-box;

          &:hover {
            border: 1px solid #e57937;
            color: $orange;
          }
        }

        &--disabled {
          color: $black-3;
          background: #ececec;
          border: 1px solid #cdcdcd;
          cursor: default !important;
        }

        &__tooltip {
          position: relative;
          display: inline-block;

          .tooltip-text {
            visibility: hidden;
            bottom: 128%;
            width: 209px;
            background-color: $white;
            color: $black-2;
            text-align: center;
            padding: 8px 12px 10px;
            font-weight: normal;
            font-size: 12px;
            line-height: 16px;
            position: absolute;
            z-index: 1;
            left: 20%;

            @include media-breakpoint-up(md) {
              left: -10%;
            }

            &:after {
              content: " ";
              position: absolute;
              top: 100%;
              left: 50%;
              margin-left: -5px;
              border-width: 5px;
              border-style: solid;
              border-color: $white transparent transparent transparent;
            }
          }

          &:hover {
            .tooltip-text {
              visibility: visible;
            }
          }
        }
      }

      &__statistics {

        &__number {
          font-weight: 600;
          font-size: 32px;
          line-height: 44px;
          color: $yellow;
          margin-bottom: 8px;
        }

        &__text {
          font-weight: normal;
          font-size: 14px;
          line-height: 19px;
          color: $black-2;
        }
      }

      &__list-inform {
        margin: 0 0 14px 0;

        @include media-breakpoint-up(md) {
          margin: 8px 33px 0 17px;
        }

        .d-block {
          &:nth-child(odd) {
            padding-left: 0;
            padding-right: 15px;

            @include media-breakpoint-up(md) {
              padding-left: 15px;
              padding-right: 15px;
            }
          }

          &:nth-child(even) {
            padding-left: 15px;
            padding-right: 0;

            @include media-breakpoint-up(md) {
              padding-left: 15px;
              padding-right: 15px;
            }
          }

          &:nth-child(4) {
            margin-bottom: 5px;
            @include media-breakpoint-up(md) {
              margin-bottom: 0;
            }
          }
        }

        &__mar-element {
          margin-bottom: 24px;
        }
      }

      &__list-btn {
        .col-md-2 {
          padding-right: 24px;
          padding-left: 0;
          max-width: 102px;
          width: 100%;
        }

        .col-md-10 {
          padding-left: 0;
        }

        &__block {
          @extend .d-flex, .flex-wrap, .flex-xl-nowrap, .justify-content-md-start, .justify-content-center;
        }
      }

      &__border-none {
        border-bottom: none !important;
      }
    }

    &__title {
      padding-bottom: 16px;
      border-bottom: 1px solid #ebecec;
      margin-bottom: 16px;
      @extend .d-md-flex, .d-block, .align-items-center, .justify-content-between;

      @include media-breakpoint-up(md) {
        margin-bottom: 24px;
        padding-bottom: 32px;
      }

      &__text {
        font-weight: 600;
        font-size: 21px;
        line-height: 29px;
        color: $black;
        margin-bottom: 16px;

        @include media-breakpoint-up(md) {
          margin-bottom: 0;
        }
      }
    }

    &__choices {

      @include media-breakpoint-up(md) {
        width: 190px;
      }
    }


    &__form {
      max-width: 454px;
      width: 100%;
      margin: 0 auto;

      .login__form__text {
        margin: 32px 0 24px;
      }

      &__text {
        margin: 16px 0 24px !important;
      }


      &__btn {
        margin: 32px 0 0;
      }
    }
  }

  &__modal {
    width: 100%;
    max-width: 982px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 30;
    box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
    text-align: center;
    border-radius: 3px;
    background-color: #fff;
    transition: 0.3s all;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 48px 26px 24px;
    height: 483px;
    justify-content: space-between;

    @include media-breakpoint-up(md) {
      padding: 72px 26px;
      height: auto;
      justify-content: flex-start;
    }

    &__padding-small {
      padding: 34px 26px 24px;

      @include media-breakpoint-up(md) {
        padding: 72px 26px;
      }
    }

    &__title {
      font-weight: bold;
      font-size: 20px;
      line-height: 27px;
      text-align: center;
      color: $yellow;
      max-width: 455px;

      @include media-breakpoint-up(md) {
        font-size: 30px;
        line-height: 41px;
      }
    }

    &__icon-title {
      margin-right: 0;
      margin-bottom: 19px;

      @include media-breakpoint-up(md) {
        margin-right: 24px;
        margin-bottom: 0;
      }
    }

    &__text {
      max-width: 751px;
      font-weight: normal;
      font-size: 14px;
      line-height: 24px;
      text-align: center;
      color: $black-2;
      margin: 32px 0 45px;

      @include media-breakpoint-up(md) {
        font-size: 18px;
        line-height: 31px;
        margin: 48px 0;
      }

      &__margin {
        margin: 24px 0 24px;

        @include media-breakpoint-up(md) {
          font-size: 18px;
          line-height: 31px;
          margin: 64px 0;
        }
      }

      &__color {
        color: $burgundy;
      }

      &__link {
        text-decoration: underline;
      }
    }

    &__btn {
      display: block;
      font-weight: 600;
      font-size: 15px;
      line-height: 20px;
      text-align: center;
      padding: 19px 0 21px;
      border: none;

      &__cancel {
        background: rgba(232, 186, 6, 0.2);
        color: $yellow;
        margin-bottom: 16px;
        margin-right: 0;
        width: 291px;

        @include media-breakpoint-up(md) {
          margin-right: 40px;
          margin-bottom: 0;
        }
      }

      &__apply {
        background: $yellow;
        color: $white;
        width: 291px;
      }

      &__big {
        display: flex;
        justify-content: center;
        text-align: center;
        width: 295px;
        height: 60px;
        background: $yellow;
        border: 1px solid #e8ba06;
        color: $white;

        @include media-breakpoint-up(md) {
          width: 347px;
        }

        &:nth-child(1) {
          margin-right: 0;
          margin-bottom: 16px;

          @include media-breakpoint-up(md) {
            margin-right: 40px;
            margin-bottom: 0;
          }
        }

        svg {
          margin-left: 16px;
        }

        &:hover {
          background: #e57937;
          border: 1px solid #e57937;
        }
      }
    }
  }
}

.hidden-mob {
  display: none !important;

  @include media-breakpoint-up(md) {
    display: block !important;
  }
}

.overlay {

  /* Ð¡ÐºÑÑÐ²Ð°ÐµÐ¼ Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÑ  */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(243, 244, 244, 0.5);
  z-index: 20;
  transition: 0.3s all;
}


/* Ð¡ÑÐ¸Ð»Ð¸ Ð´Ð»Ñ Ð¼Ð¾Ð´Ð°Ð»ÑÐ½ÑÑ Ð¾ÐºÐ¾Ð½ */

