  opacity: 0;
  visibility: hidden;
}

/* Ð¡ÑÐ¸Ð»Ð¸ Ð´Ð»Ñ Ð°ÐºÑÐ¸Ð²Ð½ÑÑ ÐºÐ»Ð°ÑÑÐ¾Ð² Ð¿Ð¾Ð´Ð»Ð¾Ð¶ÐºÐ¸ Ð¸ Ð¾ÐºÐ½Ð° */

