.slider-item {
  height: 650px;
  width: 100%;
  background-size: cover;
  padding: 100px 15px;
  &__title {
    max-width: 1000px;
    font-weight: bold;
    font-size: 56px;
    line-height: 68px;
    color: $white;
    margin-bottom: 24px;
  }
  &__text {
    font-size: 18px;
    line-height: 28px;
    color: $white;
    max-width: 950px;
    //  margin: 0 auto 69px;
  }

  @media (max-width: 1200px) {
    &__title {
      max-width: 900px;
    }
    &__text {
      max-width: 900px;
    }
  }

  @media (max-width: 992px) {
    height: 850px;
    padding: 80px 15px;
    &__title {
      margin-bottom: 16px;
    }
  }

  @media (max-width: 576px) {
    height: 590px;
    &__title {
      font-size: 36px;
      line-height: 49px;
      margin-bottom: 24px;
    }
    &__text {
      font-size: 14px;
      line-height: 19px;
    }
  }

  @media (max-width: 330px) {
    &__title {
      font-size: 33px;
      line-height: 41px;
      margin-bottom: 24px;
    }
  }
}
