.statistics {
  padding: 64px 0 82px;
  background-color: $grey;
  &__title {
    text-align: center;
    font-weight: bold;
    font-size: 38px;
    line-height: 52px;
    margin-bottom: 64px;
  }
  &__list {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    width: 292px;
    &:last-child {
      .statistics__item-text {
        width: 280px;
      }
    }
  }
  &__item-title {
    font-weight: bold;
    font-size: 68px;
    line-height: 93px;
    color: $black;
    margin-bottom: 16px;
  }
  &__item-text {
    font-size: 21px;
    line-height: 29px;
    color: $black-2;
    margin-bottom: 32px;
  }
  &__link {
    color: $yellow;
    position: relative;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    svg {
      position: absolute;
      transition: 0.3s;
      right: 0;
    }
    span {
      padding-right: 30px;
    }
    &:hover {
      color: $yellow;
      svg {
        right: -3px;
      }
    }
  }
  @media (max-width: 992px) {
    padding-bottom: 66px;
    &__title {
      margin-bottom: 40px;
    }
    &__list {
      display: block;
    }
    &__item {
      width: 100%;
      margin-bottom: 42px;
      &:last-child {
        margin-bottom: 0;
        .statistics__item-text {
          width: 100%;
        }
      }
    }
    &__item-text {
      margin-bottom: 24px;
    }
  }
  @media (max-width: 576px) {
    padding: 40px 0 42px;
    &__title {
      font-size: 30px;
      line-height: 41px;
    }
    &__item-title {
      font-size: 60px;
      line-height: 82px;
    }
    &__item-text {
      font-size: 18px;
      line-height: 25px;
    }
  }
}
