.useful-links-content {
  padding-bottom: 175px;
  &__links-wrapper {
    padding: 0 16px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 28px;
  }
  &__text {
    margin-bottom: 60px;
  }
  @media (max-width: 992px) {
    &__links-wrapper {
      grid-gap: 20px;
    }
  }
  @media (max-width: 992px) {
    padding-bottom: 75px;
    &__links-wrapper {
      grid-template-columns: 1fr;
    }
  }
}
