.map {
  //GOOGLE MAP STYLES
  .gmnoprint {
    display: none;
  }
  .gm-fullscreen-control {
    display: none;
  }
  .map-control-buttons {
    position: absolute;
    bottom: 123px;
    right: 100px;
    z-index: 1;
    @media (max-width: 768px) {
      display: none;
    }
  }
  .controls.fullscreen-control {
    top: unset !important;
    right: 100px !important;
    bottom: 268px;
    button {
      width: 48px;
      height: 48px;
      background: #fff;
      background-image: url(../../images/svg-icons/fullscreen.svg);
      background-repeat: no-repeat;
      background-position: center;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
  .controls.zoom-control {
    display: flex;
    flex-direction: column;
    right: 100px;
    padding-right: 100px;
    @media (max-width: 768px) {
      display: none !important;
    }
    .zoom-control-in {
      width: 48px;
      height: 48px;
      background: #fff;
      background-image: url(../../images/svg-icons/zoom-plus.svg);
      background-repeat: no-repeat;
      background-position: center;
      margin-bottom: 1px;
    }
    .zoom-control-out {
      width: 48px;
      height: 48px;
      background: #fff;
      background-image: url(../../images/svg-icons/zoom-minus.svg);
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  .controls.my-location {
    .find-my-location {
      width: 48px;
      height: 48px;
      background: #fff;
      background-image: url(../../images/svg-icons/my-location.svg);
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  button {
    border: transparent;
  }
  position: relative;
  #map {
    height: 900px;
    width: 100%;
    overflow: hidden;
  }
  .mobile-open-modal,
  .open-modal {
    position: absolute;
    z-index: 2;
    background: #fff;
    color: #e8ba06;
    cursor: pointer;
    svg {
      margin-left: 16px;
    }
  }
  .open-modal {
    padding: 20px 48px;
    top: 100px;
    font-size: 15px;
    line-height: 20px;
    font-weight: 600;
    &.hidden {
      display: none;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
  .mobile-open-modal {
    bottom: 100px;
    font-size: 12px;
    line-height: 17px;
    font-weight: 600;
    padding: 12px 24px;
    right: 15%;
    &.hidden {
      display: none;
    }
  }
  .map-modal {
    max-width: 541px;
    width: 100%;
    background: #fff;
    position: absolute;
    top: 100px;
    padding: 32px;
    z-index: 1;
    @media (max-width: 768px) {
      max-width: 485px;
    }
    @media (max-width: 590px) {
      max-width: 92%;
    }
    @media (max-width: 568px) {
      max-width: 87%;
    }
    &__close {
      position: absolute;
      top: -24px;
      right: -24px;
      width: 48px;
      height: 48px;
      padding: 14px;
      background: #fff;
      border-radius: 50%;
      cursor: pointer;
      // @media (max-width: 768px) {
      //   display: none;
      // }
      svg {
        width: 20px;
        height: 20px;
      }
    }
    &__search {
      margin-bottom: 27px;
      input {
        width: 100%;
        padding-left: 19px;
        border: 1px solid #ebecec;
        font-size: 15px;
        line-height: 20px;
      }
      .submit-btn {
        width: 56px;
        height: 56px;
        background-color: #e8ba06;
        padding: 20px;
      }
    }
    &__switch {
      display: flex;
      .switch-btn {
        white-space: nowrap;
        font-size: 15px;
        line-height: 20px;
        font-weight: 600;
        padding: 10px 24px;
        @media (max-width: 768px) {
          padding: 10px;
        }
        @media (max-width: 350px) {
          font-size: 12px;
        }
        &.active {
          color: #fff;
          background: #6b2a14;
        }
      }
    }
    &__info {
      position: relative;
      .brands-business,
      .map-interesting-places {
        &.hidden {
          visibility: hidden;
          opacity: 0;
          position: absolute;
          width: 100%;
        }
      }
    }
    &.hidden {
      display: none;
    }
  }
  .categories {
    margin-top: 32px;
    padding: 18px 0 32px 0;
    border-top: 1px solid #ebecec;
    border-bottom: 1px solid #ebecec;
    .flickity-button {
      top: 40%;
      background: transparent;
      &-icon {
        width: 30px;
        height: 20px;
      }
      &.next {
        @media (max-width: 768px) {
          display: none;
        }
      }
    }
  }
  .item {
    max-width: 120px;
    margin: 0 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__circle {
      cursor: pointer;
      width: 91px;
      height: 91px;
      border-radius: 50%;
      padding: 24px;
      transition: 0.3s;
      background-color: var(--m-color);
      path {
        transition: 0.3s;
      }
      &:hover {
        background-color: var(--h-color);
        path {
          fill: var(--h-fill);
        }
      }
    }
    &__title {
      margin-top: 17px;
      font-size: 13px;
      line-height: 18px;
      text-align: center;
      color: #1b1b1b;
    }
    &.active {
      .item__circle {
        background-color: var(--h-color);
        path {
          fill: var(--h-fill);
        }
      }
      .item__title {
        font-weight: 700;
      }
    }
  }
  .map-category {
    &__item {
      width: 187px;
      margin: 6px;
      &.hidden {
        display: none;
      }
    }
    &__image {
      height: 139px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__head {
      margin-top: 23px;
      margin-bottom: 30px;
      font-size: 22px;
      line-height: 30px;
      font-weight: 600;
      color: #1b1b1b;
      @media screen and (max-width: 350px) {
        font-size: 20px;
        line-height: 26px;
      }
    }
    &__title {
      font-size: 13px;
      line-height: 18px;
      font-weight: 600;
      color: #1b1b1b;
    }
    .flickity-button {
      top: -40px;
      background: transparent;
      &-icon {
        width: 30px;
        height: 20px;
      }
      &.previous {
        display: none;
      }
    }
  }
  .from-marker {
    &__rating-stars {
      margin: 26px 0;
      .count {
        margin-left: 16px;
        font-size: 14px;
        line-height: 19px;
        font-weight: 600;
        color: #e8ba06;
      }
    }
    &__forward-button {
      a {
        margin-bottom: 24px;
        font-size: 16px;
        line-height: 22px;
        font-weight: 600;
        color: #6b2a14;
      }
    }
    &__name-of-brand {
      margin-bottom: 24px;
      font-size: 21px;
      line-height: 28px;
      font-weight: 600;
      color: #1b1b1b;
    }
    &__category-of-brand {
      margin-bottom: 24px;
      font-size: 15px;
      line-height: 20px;
      font-weight: 400;
      color: #6b2a14;
    }
    &__another-text {
      margin-bottom: 24px;
      font-size: 16px;
      line-height: 26px;
      font-weight: 400;
      color: #333;
    }
    .information {
      padding: 24px 0;
      display: flex;
      border-top: 1px solid #ebecec;
      border-bottom: 1px solid #ebecec;
      @media (max-width: 768px) {
        padding: 34px 0 24px;
      }
      &__title {
        .bold-text.space {
          margin-bottom: 24px;
        }
        @media (max-width: 768px) {
          display: none;
        }
      }
      &__items {
        margin-left: 60px;
        @media (max-width: 768px) {
          margin-left: unset;
        }
        .text {
          margin-bottom: 25px;
          cursor: pointer;
          @media (max-width: 768px) {
            margin-bottom: 40px;
          }
          a {
            color: #333;
          }
        }
        .address__text {
          position: relative;
          &::before {
            margin-right: 12px;
            content: url(../../images/svg-icons/map-location.svg);
          }
          &::after {
            content: "ÐÐ´ÑÐµÑÐ°";
          }
        }
        .phone__text {
          position: relative;
          &::before {
            margin-right: 12px;
            content: url(../../images/svg-icons/map-phone.svg);
          }
          &::after {
            content: "Ð¢ÐµÐ»ÐµÑÐ¾Ð½";
          }
        }
        .mail__text {
          position: relative;
          cursor: pointer;
          a {
            color: #333;
          }
          &::before {
            margin-right: 12px;
            content: url(../../images/svg-icons/map-mail.svg);
          }
          &::after {
            content: "ÐÐ¾ÑÑÐ°";
          }
        }
        .address__text,
        .phone__text,
        .mail__text {
          &::after {
            position: absolute;
            top: -25px;
            left: 0;
            font-size: 16px;
            line-height: 22px;
            font-weight: 600;
            display: none;
            @media (max-width: 768px) {
              display: block;
            }
          }
        }
      }
      .bold-text {
        font-size: 16px;
        line-height: 22px;
        font-weight: 600;
        color: #1b1b1b;
      }
    }
    &__links {
      border-bottom: 1px solid #ebecec;
      padding: 24px 0;
      .bold-text {
        font-size: 16px;
        line-height: 22px;
        font-weight: 600;
        color: #1b1b1b;
        @media (max-width: 768px) {
          margin-bottom: unset;
        }
      }
      .links {
        margin-left: 41px;
        @media (max-width: 768px) {
          margin-left: 11px;
        }
        a {
          margin-right: 30px;
          @media (max-width: 360px) {
            margin-right: 15px;
          }
        }
      }
    }
    &__buttons {
      a {
        margin-top: 24px;
        padding: 20px 32px;
        font-size: 15px;
        line-height: 20px;
        font-weight: 600px;
        background-color: #e8ba06;
        color: #fff;
        @media (max-width: 768px) {
          display: block;
          text-align: center;
          width: 100%;
        }
        svg {
          margin-left: 4px;
        }
      }
      a:first-child {
        margin-right: 15px;
      }
    }
  }
  .map-modal-allmarkers,
  .map-modal-individualmarker {
    &.hidden {
      position: absolute;
      visibility: hidden;
      opacity: 0;
      z-index: -1;
    }
  }
  .gm-style .gm-style-iw-c {
    border-radius: 0;
    padding: unset;
    transform: translate3d(-10%, -12%, 0);
  }
  .gm-style-iw-t {
    &::after {
      content: none;
    }
  }
  .gm-ui-hover-effect {
    visibility: hidden;
  }
  .marker-popup {
    width: 400px;
    height: 92px;
    display: flex;
    .marker-logo {
      padding-top: 24px;
      padding-left: 24px;
    }
    .marker-text {
      display: flex;
      align-items: center;
      padding-left: 30px;
      p {
        margin: unset;
        font-size: 17px;
        line-height: 24px;
        font-weight: 600;
      }
    }
  }
}