a[role="link"] {
  text-decoration: underline;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;

  border-radius: 0;
  transition: 0.3s;
  svg {
    margin-left: 16px;
    path {
      transition: 0.3s;
    }
    @media (max-width: 768px) {
      margin-left: 8px;
    }
  }
  &--primary {
    color: $white;
    background: $yellow;
    border: 1px solid $yellow;
    &:hover {
      background: $orange;
      border: 1px solid $orange;
      color: $white;
    }
  }

  &--secondary {
    color: $yellow;
    background: transparent;
    border: 1px solid $yellow;
    &:hover {
      background: $yellow;
      color: $white;
      svg path {
        fill: $white;
      }
    }
  }
}
