.place-box {
  display: grid;
  grid-template-rows: 200px 1fr 1.5fr auto;
  background: $white;
  box-shadow: 0.0001px 2px 8px rgba(0, 0, 0, 0.16);
  &__img {
    overflow: hidden;
    height: 200px;
    img {
      width: 100%;
      transition: 0.3s;
      object-fit: cover;
      &:hover {
        transform: scale(1.15);
      }
    }
  }
  &__title {
    padding: 20px 20px 10px;
    h2 {
      margin: 0;
    }
  }
  /*
  &__info {
    padding: 20px 20px 0;
    h2 {
      font-size: 18px;
      line-height: 25px;
      margin-bottom: 16px;
      font-weight: 600;
    }
    p {
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 24px;
    }
  }
  */
  &__about {
    padding: 0 20px;
    p {
      display: grid;
      grid-template-columns: auto 1fr;
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 24px;
      color: $black-2;
    }
    a {
      display: block;
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 24px;
      color: $black-2;
    }
    svg {
      width: 22px;
      margin-right: 12px;
    }
  }
  &__bottom {
    padding: 0 20px;
  }
  &__social {
    display: flex;
    align-items: baseline;
    justify-content: flex-start;
    margin-bottom: 27px;
    li {
      margin-right: 26px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__nav {
    align-self: flex-end;
    display: flex;
    gap: 10px;
    .btn--secondary {
      margin-bottom: 20px;
    }
    a {
      padding: 5px;
      font-size: 14px;
      height: 60px;
      justify-content: center;
    }
    svg {
      margin: 0;
      width: 30px;
    }
  }
  @media (max-width: 1200px) {
    grid-template-rows: 150px 1fr 1.5fr auto;
    &__img {
      height: 150px;
    }
    &__nav a {
      font-size: 13px;
    }
  }

  @media (max-width: 992px) {
    grid-template-rows: auto auto;
    &__img {
      height: 300px;
      img {
        height: 100%;
        object-fit: cover;
      }
    }
    &__title {
      padding: 16px 16px 5px;
    }
    &__bottom {
      padding: 0 16px;
    }
    &__about {
      padding: 0 16px;
      a,
      p {
        line-height: 22px;
      }
    }
    &__nav {
      gap: 16px;
      a {
        width: 50%;
      }
      svg {
        margin-left: 16px;
      }
    }
  }

  @media (max-width: 768px) {
    &__bottom {
      padding: 0 16px 12px;
    }
    &__nav {
      display: block;
      a {
        width: 100%;
      }
    }
  }
}
